.offers {
	padding: 70px 0 70px;
	background: #f0f3ff;

	&_title {
		text-align: center;
		font-weight: bold;
		font-size: 45px;
		line-height: 57px;
		margin-bottom: 37px;
	}

	&_items {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr 1fr;
		grid-gap: 20px;
	}

	&_item {
		background: #ffffff;
		min-height: 250px;
		box-shadow: 0px 20px 40px rgba(59, 86, 215, 0.07);
		border-radius: 8px;
		padding: 33px 30px 32px;

		svg {
			display: block;
			margin: 0 auto;
			margin-bottom: 18px;
		}

		p.offers_text {
			font-size: 28px;
			line-height: 30px;
			text-align: center;
			color: #3b56d7;
		}
	}
}

// ===============================
// =========== @Media ============
// ===============================

@media (max-width: $xl) {
	.offers {
	}
}

@media (max-width: $lg) {
	.offers {
	}
}

@media (max-width: $md) {
	.offers {
		padding: 60px 0 60px;
		background: #f0f3ff;

		&_title {
			max-width: 80%;
			margin: 0 auto;
			font-size: 36px;
			line-height: 42px;
			margin-bottom: 34px;
		}

		&_items {
			display: grid;
			grid-template-columns: 1fr 1fr 1fr 1fr;
			grid-gap: 22px;
		}

		&_item {
			background: #ffffff;
			min-height: 165px;
			padding: 15px 0px 18px;

			svg {
				width: 130px;
				max-height: 70px;
				margin-bottom: 15px;
			}

			p.offers_text {
				font-size: 20px;
				line-height: 22px;
			}
		}
	}
}

@media (max-width: $sm) {
	.offers {
		padding: 35px 0;

		&_title {
			max-width: 100%;
			width: 100%;
			font-size: 20px;
			line-height: 24px;
			margin-bottom: 11px;
		}

		&_items {
			display: grid;
			grid-template-columns: 1fr 1fr;
			grid-gap: 12px;
		}

		&_item {
			background: #ffffff;
			min-height: 130px;
			padding: 15px 5px 13px;

			svg {
				width: 120px;
				max-height: 50px;
				margin-bottom: 15px;
			}

			p.offers_text {
				font-size: 16px;
				line-height: 18px;
			}
		}
	}
}
