.faq {
	padding: 71px 0 72px;
	background: #f0f3ff;

	&_title {
		font-weight: bold;
		font-size: 45px;
		line-height: 55px;
		text-align: center;
		margin-bottom: 36px;
	}

	&_no-answer {
		display: flex;
		justify-content: center;
		align-items: center;
	}

	&_no-answer-text {
		font-size: 24px;
		line-height: 29px;
		color: #8c8c8c;
		margin-right: 35px;
	}

	&_no-answer-button {
		width: 249px;
		font-weight: bold;
		font-size: 24px;
		line-height: 29px;
		padding: 16px 0 15px;
		text-align: center;
	}

	.Dropdown {
		background: #ffffff;
		border-radius: 8px;
		padding: 20px 30px 10px 41px;
		cursor: pointer;
		transition: 0.3s linear;
		-webkit-tap-highlight-color: transparent;
		margin-bottom: 30px;
		border: 1px solid #3b56d7;

		&:last-child {
			margin-bottom: 37px;
		}

		&__wrapper {
			display: flex;
			justify-content: space-between;
			align-items: center;
			pointer-events: none;
		}

		&__title {
			width: calc(100% - 74px);
			font-weight: bold;
			font-size: 28px;
			line-height: 34px;
			color: #3b56d7;
			pointer-events: none;
		}

		&__icon {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 50px;
			height: 50px;
			border-radius: 50%;
			border: 1px solid #3b56d7;
			pointer-events: none;

			svg {
				position: relative;
				transition: 0.3s linear;
				transform: rotate(180deg);
			}
		}

		&__text {
			font-size: 24px;
			line-height: 29px;
			color: #8c8c8c;
			overflow: hidden;
			max-height: 0px;
			transition: 0.3s linear;
			padding: 10px 0 0 0;
			max-width: 90%;
			pointer-events: none;
		}

		&--active {
			padding-bottom: 17px;
			padding-top: 35px;
			box-shadow: 0px 20px 50px rgba(59, 86, 215, 0.25);
			border: 0;

			.Dropdown__title {
				// font-weight: bold;
			}
		}

		&--active .Dropdown__icon svg {
			transform: rotate(180deg) scale(1, -1);
		}

		&--active .Dropdown__text {
			padding: 20px 0 25px;
			max-height: 400px;
		}
	}
}
// ===============================
// =========== @Media ============
// ===============================

@media (max-width: $xl) {

}

@media (max-width: $lg) {

}

@media (max-width: $md) {
	.faq {
		padding: 60px 0;
		background: #f0f3ff;

		&_title {
			font-size: 40px;
			line-height: 49px;
			margin-bottom: 41px;
		}

		&_no-answer-text {
			font-size: 20px;
			line-height: 24px;
			color: #8c8c8c;
			margin-right: 35px;
		}

		&_no-answer-button {
			width: 196px;
			font-size: 18px;
			line-height: 22px;
			padding: 13px 0 15px;
		}

		.Dropdown {
			
			&:last-child {
				margin-bottom: 64px;
			}

			&__wrapper {
				display: flex;
				justify-content: space-between;
				align-items: center;
				pointer-events: none;
			}

			&__title {
				width: calc(100% - 120px);
				font-size: 22px;
				line-height: 27px;
			}

			&__icon {
				width: 40px;
				height: 40px;
			}

			&__text {
				max-width: 95%;
				font-size: 18px;
				line-height: 22px;
			}

			&--active {
				padding-bottom: 5px;
				padding-top: 20px;
				box-shadow: 0px 20px 50px rgba(59, 86, 215, 0.25);
				border: 0;
			}

			&--active .Dropdown__text {
				padding: 20px 0 25px;
				max-height: 400px;
			}
		}
	}
}

@media (max-width: $sm) {
	
	.faq {
		padding: 35px 0;
		background: #f0f3ff;

		&_title {
			font-size: 20px;
			line-height: 24px;
			margin-bottom: 15px;
		}
		
		&_no-answer {
			display: block;
		}

		&_no-answer-text {
			width: 100%;
			text-align: center;
			font-size: 16px;
			line-height: 19px;
			color: #8c8c8c;
			margin-right: 0px;
			margin-bottom: 15px;
		}

		&_no-answer-button {
			display: block;
			width: 100%;
			height: 40px;
			padding: 9px 0;
		}

		.Dropdown {
			padding: 14px 11px 10px 15px;
			margin-bottom: 15px;
			
			&:last-child {
				margin-bottom: 15px;
			}

			&__wrapper {
				align-items: flex-start;
				pointer-events: none;
			}

			&__title {
				width: calc(100% - 60px);
				font-weight: normal;
				font-size: 18px;
				line-height: 22px;
				padding-top: 9px;
			}

			&__icon {
				top: 17px;
				width: 28px;
				height: 28px;
			}

			&__text {
				max-width: 100%;
				font-size: 16px;
				line-height: 19px;
			}

			&--active {
				padding-bottom: 5px;
				padding-top: 14px;
				box-shadow: 0px 20px 50px rgba(59, 86, 215, 0.25);
				border: 0;
			}

			&--active .Dropdown__text {
				padding: 20px 0 5px;
				max-height: 400px;
			}
		}
	}
}
