.main-head {
	position: relative;
	background: url("./../img/main-head.png") no-repeat center / cover;
	padding: 372px 0 153px;

	&::after {
		content: "";
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background: rgba(46, 48, 57, 0.7);
	}

	& * {
		color: #ffffff;
	}

	&_content {
		position: relative;
		z-index: 1;
	}

	&_title {
		font-size: 55px;
		line-height: 67px;
		margin-bottom: 20px;
	}

	&_text {
		font-size: 28px;
		line-height: 30px;
	}

	&_button {
		display: block;
		width: 250px;
		text-align: center;
		padding: 18px 0 19px;
		font-weight: bold;
		font-size: 24px;
		line-height: 29px;
	}

	&_slider {
		margin-bottom: 75px;
	}

	&_slide {
		outline: none;
	}

	.slick-arrow {
		position: absolute;
		background: url("./../img/big-slider-arrow.svg") no-repeat center / cover;
		font-size: 0;
		width: 26px;
		height: 46px;
		top: 44px;

		&.slick-disabled {
			opacity: 0.6;

			&:hover {
				cursor: default;
			}
		}

		&.slick-prev {
			left: -170px;
		}

		&.slick-next {
			right: -170px;
			transform: rotate(180deg);
		}

		&:hover {
			cursor: pointer;
		}
	}

	.slick-dots {
		position: relative;
		z-index: 1;
		display: flex;
		justify-content: center;
		background: none;

		li {
			margin: 0 8px 0 0;

			&:last-child {
				margin-right: 0;
			}
		}

		li {
			button {
				width: 10px;
				height: 10px;
				border: 1px solid #ffffff;
				border-radius: 50%;
				font-size: 0;
				padding: 0;
				background: transparent;
			}

			&.slick-active button {
				background: white;
			}
		}
	}
}

// ===============================
// =========== @Media ============
// ===============================

@media (max-width: $xl) {
	.main-head {
		.slick-arrow {
			&.slick-prev {
				left: -80px;
			}

			&.slick-next {
				right: -80px;
				transform: rotate(180deg);
			}
		}
	}
}

@media (max-width: 1350px) {
	.main-head {
		.slick-arrow {
			display: none !important;

			&.slick-prev {
				left: -80px;
			}

			&.slick-next {
				right: -80px;
				transform: rotate(180deg);
			}
		}
	}
}

@media (max-width: $lg) {
	.main-head {
	}
}

@media (max-width: $md) {
	.main-head {
		padding: 213px 0 75px;

		&_title {
			font-size: 36px;
			line-height: 44px;
			margin-bottom: 9px;
		}

		&_text {
			max-width: 60%;
			font-size: 18px;
			line-height: 24px;
		}

		&_slider {
			margin-bottom: 28px;
		}

		&_button {
			display: block;
			width: 196px;
			text-align: center;
			padding: 13px 0 15px;
			font-weight: bold;
			font-size: 18px;
			line-height: 22px;
		}

		.slick-dots {
			top: 50px;
		}
	}
}

@media (max-width: $sm) {
	.main-head {
		padding: 93px 0 60px;

		&_title {
			font-size: 20px;
			line-height: 24px;
		}

		&_text {
			max-width: 100%;
			font-size: 16px;
			line-height: 18px;
		}

		&_slider {
			margin-bottom: 40px;
		}

		&_button {
			width: 155px;
			padding: 11px 0 11px;
			font-size: 16px;
			line-height: 19px;
		}

		.slick-dots {
			top: 25px;
		}
	}
}
