*,
*::before,
*::after {
	box-sizing: border-box;
}

html {
	font-size: 100%;
	-webkit-font-smoothing: antialiased;
	overflow-x: hidden;
}

body {
	font-family: "ProximaNova", sans-serif;
	overflow-x: hidden;
	color: $black;
}

ul {
	list-style-type: none;
	padding: 0;
	margin: 0;
}

a {
	text-decoration: none;
	transition: 0.3s linear;
}

select {
	border: none;
}

select:active,
select:focus {
	outline: none;
}

select:focus > option {
	outline: 0;
}

input:focus,
textarea:focus {
	border: none;
	outline: none;
}

h1,
h2,
h3,
h4 {
	font-weight: 400;
}

h1 {
	font-weight: bold;
	font-size: 45px;
	line-height: 55px;
	color: $black;

	@media (max-width: 980px) {
		font-size: 36px;
		line-height: 44px;
	}

	@media (max-width: 540px) {
		font-size: 28px;
		line-height: 34px;
	}
}

h2 {
	font-size: 40px;
	line-height: 49px;

	@media (max-width: 980px) {
		font-size: 32px;
		line-height: 39px;
	}

	@media (max-width: 540px) {
		font-weight: 600;
		font-size: 26px;
		line-height: 32px;
	}
}

h3 {
	font-size: 36px;
}

h4 {
	font-weight: bold;
	font-size: 20px;
	line-height: 24px;

	@media (max-width: 980px) {
		font-size: 18px;
		line-height: 22px;
	}

	@media (max-width: 540px) {
		font-size: 20px;
		line-height: 24px;
	}
}

p {
	font-size: 20px;
	line-height: 24px;
	margin: 0;
}

.unscrollable {
	overflow: hidden;
}

.container {
	max-width: 1220px;
	padding: 0 20px;
	margin: 0 auto;

	&__footer {
		@media (max-width: 540px) {
			padding: 0 16px;
		}
	}

	&__legal-data {
		max-width: 543px;
		padding: 0;

		@media (max-width: $sm) {
			padding: 0 20px;
		}
	}

	&__padding-16 {
		@media (max-width: $sm) {
			padding: 0 16px;
		}
	}
}

.hidden {
	display: none !important;
}

.desktop-hidden {
	@media (min-width: 541px) {
		display: none !important;
	}
}

.mobile-hidden {
	@media (max-width: 540px) {
		display: none !important;
	}
}

.rouble {
	font-family: "rouble";
}
