.platform {
	padding: 70px 0;

	&_title {
		font-weight: bold;
		font-size: 45px;
		line-height: 55px;
		margin-bottom: 45px;
		text-align: center;
	}

	.platform_slide {
		outline: none;
		border: 0;

		img {
			display: block;
			margin: 0 auto;
		}
	}

	.slick-track {
		display: flex;
		align-items: center;
	}

	.slick-arrow {
		position: absolute;
		background: url("./../img/slider-arow-dark.svg") no-repeat center / cover;
		font-size: 0;
		width: 26px;
		height: 46px;
		top: 20px;

		&.slick-disabled {
			opacity: 0.6;

			&:hover {
				cursor: default;
			}
		}

		&.slick-prev {
			left: -170px;
		}

		&.slick-next {
			right: -170px;
			transform: rotate(180deg);
		}

		&:hover {
			cursor: pointer;
		}
	}
}

// ===============================
// =========== @Media ============
// ===============================

@media (max-width: $xl) {
	.platform {
		.slick-arrow {
			&.slick-prev {
				left: -80px;
			}

			&.slick-next {
				right: -80px;
				transform: rotate(180deg);
			}
		}
	}
}

@media (max-width: 1350px) {
	.platform {
		.slick-arrow {
			&.slick-prev {
				left: -50px;
			}

			&.slick-next {
				right: -50px;
				transform: rotate(180deg);
			}
		}
	}
}

@media (max-width: $lg) {
	.platform {
		padding: 60px 0 50px 0;

		&_title {
			font-size: 36px;
			line-height: 44px;
			margin-bottom: 32px;
		}

		&_content {
			padding: 0 55px;
		}

		.slick-arrow {
			width: 21px;
			height: 37px;

			&.slick-prev {
				left: -62px;
			}

			&.slick-next {
				right: -62px;
				transform: rotate(180deg);
			}
		}

		.platform_slide {
			outline: none;
			border: 0;

			img {
				max-width: 170px;
			}
		}
	}
}

@media (max-width: $md) {
}

@media (max-width: $sm) {
	.platform {
		padding: 35px 0 50px 0;
		position: relative;

		&_title {
			max-width: 80%;
			margin: 0 auto;
			font-size: 20px;
			line-height: 24px;
			margin-bottom: 5px;
		}

		&_content {
			padding: 0;
		}

		.platform_slide {
			outline: none;
			border: 0;

			img {
				max-width: 120px;
			}
		}

		.slick-dots {
			position: absolute;
			display: flex;
			justify-content: center;
			width: 100%;
			bottom: 30px;
			z-index: 1;
			background: none;

			li {
				margin: 0 8px 0 0;

				&:last-child {
					margin-right: 0;
				}
			}

			li {
				button {
					width: 10px;
					height: 10px;
					border: 1px solid #ababab;
					border-radius: 50%;
					font-size: 0;
					padding: 0;
					background: transparent;
				}

				&.slick-active button {
					background: #ababab;
				}
			}
		}
	}
}
