.cases {
	&_title {
		margin-bottom: 18px;
	}

	&_cards {
		margin-bottom: 45px;
	}

	&_card {
		display: flex;
		justify-content: flex-start;
		align-items: flex-start;
		position: relative;
		background: #ffffff;
		box-shadow: 0px 20px 50px rgba(59, 86, 215, 0.05);
		border-radius: 8px;
		padding: 23px 31px 32px 25px;
		margin-bottom: 20px;

		// @media (max-width: 980px) {
		// 	width: 345px;
		// 	min-width: 345px;
		// }

		&:last-child {
			margin-bottom: 0;
		}
	}

	&_card-tag {
		position: absolute;
		top: 29px;
		right: 31px;
		font-size: 18px;
		line-height: 22px;
		color: #3b56d7;
		background: #e5f1ff;
		border-radius: 5px;
		padding: 5px 14px;
		transition: 200ms;

		&:hover {
			cursor: pointer;
			color: #ffffff;
			background: #3b56d7;
		}
	}

	&_card-image {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		width: 400px;
		min-width: 400px;
		height: 280px;
		margin-right: 24px;

		&.empty {
			border: 1px solid #cccccc;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}

		svg {
			margin-bottom: 9px;
		}

		p {
			font-size: 16px;
			line-height: 19px;
			color: #a4a4a4;
		}
	}

	&_card-info {
		width: calc(100% - 424px);
	}

	&_card-title {
		font-size: 24px;
		line-height: 29px;
		color: #3b56d7;
		margin-bottom: 3px;
	}

	&_card-descrition {
		font-size: 20px;
		line-height: 24px;
		color: #8c8c8c;
		margin-bottom: 18px;
	}

	&_card-slider {
		width: 100%;
		border: 1px solid #dedede;
		box-sizing: border-box;
		border-radius: 6px;
		display: flex;
		justify-content: flex-start;
		align-items: flex-start;
		flex-wrap: nowrap;
		padding: 17px 30px 17px 19px;
		margin-bottom: 25px;
	}

	&_card-slider-item {
		width: 15.5%;
		margin-right: 5.625%;

		&:last-child {
			margin-right: 0;
		}

		div {
			font-weight: bold;
			font-size: 30px;
			line-height: 37px;
			color: #3c3c3c;
			margin-bottom: 5px;
		}

		p {
			font-size: 18px;
			line-height: 22px;
			color: #8c8c8c;
		}
	}

	&_card-button {
		display: inline-block;
		width: 260px;
		font-weight: bold;
		font-size: 20px;
		line-height: 24px;
		text-align: center;
		padding: 16px 0 14px;
	}

	&_show-more {
		display: block;
		align-items: center;
		width: 210px;
		font-weight: bold;
		font-size: 24px;
		line-height: 29px;
		text-align: center;
		padding: 19px 0;
		margin: 0 auto;
	}
}

// ===============================
// =========== @Media ============
// ===============================

@media (max-width: $lg) {
	.cases {
		&_card-slider-item {
			width: 15.5%;
			margin-right: 5.625%;

			&:last-child {
				margin-right: 0;
			}

			div {
				font-weight: bold;
				font-size: 24px;
				line-height: 30px;
			}

			p {
				font-size: 16px;
				line-height: 20px;
			}
		}

		&_card-tag {
			top: 15px;
			right: 15px;
		}
	}
}

@media (max-width: $md) {
	.cases {
		&_cards {
			margin-bottom: 45px;
		}

		&_card {
			display: flex;
			justify-content: flex-start;
			align-items: flex-start;
			position: relative;
			background: #ffffff;
			box-shadow: 0px 20px 50px rgba(59, 86, 215, 0.05);
			border-radius: 8px;
			padding: 30px 40px 22px 8px;
			margin-bottom: 30px;
		}

		&_card-tag {
			font-size: 16px;
			line-height: 19px;
			top: 28px;
			right: 39px;
			padding: 3px 9px 2px 8px;

			&:hover {
				cursor: pointer;
				color: #ffffff;
				background: #3b56d7;
			}
		}

		&_card-image {
			width: 345px;
			min-width: 345px;
			height: 236px;
			margin-right: 11px;
			margin-top: 5px;

			&.empty {
				border: 1px solid #cccccc;
			}

			img {
				width: 100%;
				height: 100%;
				object-fit: contain;
			}

			svg {
				margin-bottom: 9px;
			}

			p {
				font-size: 16px;
				line-height: 19px;
				color: #a4a4a4;
			}
		}

		&_card-info {
			width: calc(100% - 358px);
		}

		&_card-title {
			font-size: 22px;
			line-height: 27px;
			margin-bottom: 7px;
		}

		&_card-descrition {
			font-size: 16px;
			line-height: 19px;
			margin-bottom: 21px;
		}

		&_card-slider {
			position: relative;
			border: 1px solid #dedede;
			box-sizing: border-box;
			border-radius: 6px;
			display: block;
			padding: 13px 0 13px 0;
			margin-bottom: 30px;

			&.slick-dotted {
				margin-bottom: 30px;
			}

			.slick-list {
				width: 100%;
			}

			.slick-track {
				// padding-left: 20px;
			}

			.slick-slide {
				padding-left: 15px;
				padding-right: 10px;
				// padding-right: 15px;
			}

			.slick-dots {
				position: absolute;
				display: block;
				left: 0;
				width: 100%;
				bottom: -15px;
				display: flex;
				align-items: flex-start;
				justify-content: center;

				li {
					width: 8px;
					height: 8px;
					margin-right: 10px;

					&:last-child {
						margin-right: 0;
					}

					&.slick-active button {
						background: rgb(196, 196, 196);
					}
				}

				li button {
					display: inline-block;
					vertical-align: top;
					width: 8px;
					height: 8px;
					font-size: 0;
					border-radius: 50%;
					background: rgba(196, 196, 196, 0.5);
					padding: 0;

					&.slick-active {
						background: rgb(196, 196, 196);
					}
				}
			}
		}

		&_card-slider-item {
			width: 15.5%;
			margin-right: 5.625%;

			&:last-child {
				margin-right: 0;
			}

			div {
				font-size: 24px;
				line-height: 29px;
				margin-bottom: 1px;
			}

			p {
				font-size: 14px;
				line-height: 17px;
			}
		}

		&_card-button {
			width: 180px;
			font-weight: 500;
			font-size: 16px;
			line-height: 19px;
			padding: 13px 0 12px;
		}

		&_show-more {
			width: 210px;
			font-size: 16px;
			line-height: 19px;
			padding: 16px 0 15px;
			margin: 0 auto;
		}
	}
}

@media (max-width: $sm) {
	.cases {
		&_title {
			margin-bottom: 21px;
		}

		&_tags {
			margin-bottom: 16px;
		}

		&_tag {
			padding: 7px 9px 4px 9px;
			margin-right: 10px;
			margin-bottom: 10px;
			font-size: 16px;
			line-height: 19px;
		}

		&_cards {
			margin-bottom: 30px;
		}

		&_card {
			display: block;
			margin-bottom: 20px;
			padding: 16px 11px 13px 12px;
			box-shadow: 0px 20px 50px rgba(59, 86, 215, 0.25);

			&:last-child {
				margin-bottom: 0;
			}
		}

		&_card-tag {
			position: static;
			display: inline-block;
			height: 26px;
			font-size: 14px;
			padding: 2px 9px 6px 9px;
			margin: 0;
		}

		&_card-image {
			width: 100%;
			min-width: 100%;
			height: auto;
			margin-bottom: 12px;

			&.empty {
				height: 187px;
			}

			img {
			}

			svg {
				width: 40px;
				height: 40px;
				margin-bottom: 4px;
			}

			p {
				font-size: 14px;
				line-height: 17px;
