		&_card-slider {
			position: relative;
			border: 1px solid #dedede;
			box-sizing: border-box;
			border-radius: 6px;
			display: block;
			padding: 21px 0 20px 0;
			margin-bottom: 17px;

			&.slick-dotted {
				margin-bottom: 43px;
			}

			.slick-list {
				width: 100%;
			}

			.slick-track {
				// padding-left: 20px;
			}

			.slick-slide {
				padding-left: 15px;
				padding-right: 10px;
				// padding-right: 15px;
			}

			.slick-dots {
				position: absolute;
				display: block;
				left: 0;
				width: 100%;
				bottom: -23px;
				display: flex;
				align-items: flex-start;
				justify-content: center;

				li {
					width: 8px;
					height: 8px;
					margin-right: 12px;

					&:last-child {
						margin-right: 0;
					}

					&.slick-active button {
						background: rgb(196, 196, 196);
					}
				}

				li button {
					display: inline-block;
					vertical-align: top;
					width: 8px;
					height: 8px;
					font-size: 0;
					border-radius: 50%;
					background: rgba(196, 196, 196, 0.5);
					padding: 0;

					&.slick-active {
						background: rgb(196, 196, 196);
					}
				}
			}
		}

		&_card-slider-item {
			// padding: 0 28px 0 22px;

			&:last-child {
				margin-right: 0;
			}

			div {
				font-size: 24px;
				line-height: 29px;
				margin-bottom: 0;
			}

			p {
				max-width: 90%;
				font-size: 16px;
				line-height: 18px;
			}
		}

		&_card-button {
			height: 40px;
			font-size: 18px;
			line-height: 22px;
			padding: 7px 0 8px 0;
