.white-form {
	background: #f7f9ff;
	padding: 55px 0;

	@media (max-width: 540px) {
		background: white;
		padding: 0;
	}

	&_title {
		font-size: 40px;
		line-height: 49px;
		font-weight: bold;
		text-align: center;
		margin-bottom: 32px;

		@media (max-width: 540px) {
			font-size: 28px;
			line-height: 34px;
			margin-bottom: 18px;
		}
	}

	&_content {
		background: #ffffff;
		border-radius: 6px;
		padding: 45px 15%;

		@media (max-width: 540px) {
			background: white;
			border-radius: 0;
			padding: 19px 0 44px 0;
		}
	}

	.grid {
		display: grid;
		grid-template-columns: 1fr 1fr;
		grid-gap: 20px;

		@media (max-width: 540px) {
			display: block;
		}
	}

	input {
		border: 1px solid #cbcbcb;
		box-sizing: border-box;
		border-radius: 4px;
		padding: 8px 17px 6px;
		font-size: 20px;
		line-height: 24px;

		@media (max-width: 540px) {
			width: 100%;
			font-size: 16px;
			line-height: 19px;
			padding: 10px 17px 8px;
			margin-bottom: 16px;
		}

		&::-webkit-input-placeholder {
			color: #8c8c8c;
		}
		&::-moz-placeholder {
			color: #8c8c8c;
		}
		&:-moz-placeholder {
			color: #8c8c8c;
		}
		&:-ms-input-placeholder {
			color: #8c8c8c;
		}
	}

	textarea {
		display: block;
		overflow: auto;
		font-size: 20px;
		line-height: 24px;
		resize: none;
		border: 1px solid #cbcbcb;
		border-radius: 4px;
		padding: 13px 17px 11px;

		@media (max-width: 540px) {
			display: none;
		}

		&::-webkit-input-placeholder {
			color: #8c8c8c;
		}
		&::-moz-placeholder {
			color: #8c8c8c;
		}
		&:-moz-placeholder {
			color: #8c8c8c;
		}
		&:-ms-input-placeholder {
			color: #8c8c8c;
		}
	}

	.full-width {
		grid-column: span 2 / auto;
	}

	&_confidence {
		width: 100%;
		font-size: 20px;
		line-height: 24px;
		text-align: center;
		color: #8c8c8c;
		margin-bottom: 20px;

		@media (max-width: 540px) {
			font-size: 16px;
			line-height: 19px;
			margin-bottom: 22px;
		}

		a {
			display: inline;
			color: $light_green;

			&:hover {
				cursor: pointer;
			}
		}
	}

	&_button {
		display: block;
		height: 60px;
		width: 400px;
		font-weight: bold;
		font-size: 24px;
		line-height: 29px;
		text-align: center;
		background: $light_green;
		border-radius: 4px;
		color: #ffffff;
		padding: 19px 0;
		margin: 0 auto;

		@media (max-width: 540px) {
			width: 100%;
			height: auto;
			font-size: 20px;
			line-height: 24px;
			padding: 6px 0 6px 0;
		}

		&:hover {
			cursor: pointer;
		}
	}

	input[type="file"] {
		height: 0;
	}

	.input__wrapper {
		width: 100%;
		position: relative;
		margin: 0;
		text-align: center;
		border: 1px solid #cbcbcb;
		border-radius: 4px;

		@media (max-width: 540px) {
			margin-bottom: 13px;
		}
	}

	.input__file {
		opacity: 0;
		visibility: hidden;
		position: absolute;
	}

	.input__file-icon-wrapper {
		height: 40px;
		width: 40px;
		margin-right: 10px;
		padding-left: 15px;
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-align: center;
		-ms-flex-align: center;
		align-items: center;
		-webkit-box-pack: center;
		-ms-flex-pack: center;
		justify-content: center;
		border-right: 1px solid #fff;
	}

	.input__file-button-text {
		padding-top: 2px;
		line-height: 40px;
		font-size: 20px;
		color: #8c8c8c;

		@media (max-width: 540px) {
			padding-top: 1px;
			font-size: 16px;
		}
	}

	.input__file-button {
		width: 100%;
		height: 40px;
		background: white;
		color: $black;
		font-size: 1.125rem;
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-align: left;
		-ms-flex-align: left;
		align-items: left;
		-webkit-box-pack: start;
		-ms-flex-pack: start;
		justify-content: flex-start;
		border-radius: 4px;
		cursor: pointer;
		margin: 0 auto;
	}
}
