.blog {
	&_title {
		margin-bottom: 19px;
	}

	&_header {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding-bottom: 20px;
		border-bottom: 1px solid #dedede;
		margin-bottom: 37px;
	}

	&_header-info {
		display: flex;
		justify-content: left;
		align-items: center;
	}

	&_header-info-item {
		display: flex;
		align-items: center;
		font-size: 18px;
		line-height: 22px;
		color: #8c8c8c;
		margin-right: 20px;

		&:last-child {
			margin-right: 0;
		}

		svg {
			margin-right: 10px;
		}
	}

	&_header-social {
		display: flex;
		justify-content: center;
		align-items: right;
		padding: 9px 12px 10px 17px;
		background: #f1f1f1;
		border-radius: 4px;
	}

	&_header-social-icon {
		display: flex;
		justify-content: center;
		align-items: center;
		height: 26px;
		width: 26px;
		background: #3b56d7;
		border-radius: 6px;
		margin-right: 16px;

		&:hover {
			opacity: 0.8;
		}

		&:nth-child(3) {
			margin-right: 18px;
		}
	}

	&_header-social-share {
		position: relative;
		top: 2px;
		right: 1px;
	}

	&_text-content {
		max-width: 900px;
	}

	&_small-title {
		margin-bottom: 10px;
	}

	&_text {
		font-size: 20px;
		line-height: 24px;
		margin-bottom: 14px;
		
		&.blog_text__bigger-line-height {
			line-height: 26px;
		}

		&__spaced-bottom41 {
			margin-bottom: 41px;
		}

		&__spaced-bottom13 {
			margin-bottom: 13px;
		}

		&__spaced-bottom26 {
			margin-bottom: 26px;
		}

		&__spaced-bottom39 {
			margin-bottom: 39px;
		}

		&__spaced-bottom31 {
			margin-bottom: 31px;
		}

		&__spaced-bottom29 {
			margin-bottom: 29px;
		}

		&__spaced-bottom21 {
			margin-bottom: 21px;
		}

		&__spaced-bottom47 {
			margin-bottom: 47px;
		}

		&__spaced-bottom104 {
			margin-bottom: 104px;
		}
	}

	&_list {
		margin-top: 12px;
		margin-bottom: 30px;
		margin: 19px 0 30px 20px;

		&__ordered .blog_list-item span:first-child {
			position: absolute;
			width: 12px;
			left: 0;
			font-size: 20px;
			line-height: 24px;
			color: #c8c8c8;
			text-align: right;
		}

		&__unordered {
			margin-bottom: 34px;

			.blog_list-item span:first-child {
				position: absolute;
				left: 0;
				top: 10px;
				width: 6px;
				height: 6px;
				border-radius: 50%;
				background: #333333;
			}
		}
	}

	&_list-item {
		position: relative;
		margin-bottom: 7px;

		a {
			display: inline-block;
			font-size: 20px;
			line-height: 24px;
			color: #3b56d7;
			margin-left: 23px;

			&:after {
				content: "";
				width: 100%;
				height: 1px;
				display: block;
				background: #3b56d7;
				transition: 0.4s linear;
				margin: 0 auto;
			}

			&:hover {
				opacity: 0.8;

				&:after {
					opacity: 0.8;
				}
			}
		}

		span:first-child {
			position: absolute;
			left: 0;
			font-size: 20px;
			line-height: 24px;
			color: #c8c8c8;
			margin-right: 11px;
		}
	}

	&_medium-title {
		margin-bottom: 20px;
	}

	&_big-image-slider {
		margin-bottom: 71px;

		.slick-list {
			width: 80%;
			margin: 0 auto;
		}

		.slick-prev {
			position: absolute;
			width: 26px;
			height: 46px;
			top: calc(50% - 23px);
			left: 0px;
			z-index: 10;
			background: url("./../img/slider-arrow.svg");
			font-size: 0;
			transition: 200ms;

			@media (max-width: 540px) {
				display: none;
			}

			&:hover {
				cursor: pointer;
				opacity: 0.8;
			}

			&.slick-disabled {
				opacity: 0.4;

				&:hover {
					cursor: default;
				}
			}
		}

		.slick-dots {
			display: none !important;
		}

		.slick-next {
			position: absolute;
			width: 26px;
			height: 46px;
			top: calc(50% - 23px);
			right: 0px;
			z-index: 10;
			background: url("./../img/slider-arrow.svg");
			font-size: 0;
			transform: rotate(180deg);
			transition: 200ms;

			@media (max-width: 540px) {
				display: none;
			}

			&:hover {
				cursor: pointer;
				opacity: 0.8;
			}

			&.slick-disabled {
				opacity: 0.4;

				&:hover {
					cursor: default;
				}
			}
		}

		&__wrapper {
			position: relative;
		}
	}

	&_big-image-slider_counter {
		position: absolute;
		height: 26px;
		z-index: 10;
		bottom: -42px;
		left: 48%;
		color: white;
		font-size: 20px;
		line-height: 26px;
		color: #8c8c8c;
	}

	&_big-image {
		display: block;
		width: 80%;
		margin: 0 auto 57px auto;
	}
	
	&_table {
		width: 100%;
		margin-bottom: 59px;
		border-collapse: collapse;
		
		thead {
			font-weight: 600;
			font-size: 18px;
			line-height: 26px;
			color: #BABEC8;
			
			td {
				padding-bottom: 16px;
			}
		}
		
		tbody tr:nth-child(odd) {
			background: #F1F4F6;
		}
		
		td {
			padding: 22px 0 22px;
			font-size: 19px;
			line-height: 26px;
			
			&:first-child {
				width: 24.5%;
				padding-left: 30px;
			}
			
			&:nth-child(2) {
				width: 25.3%;
			}
			
			&:nth-child(3) {
				width: 23%;
			}
			
			&:last-child {
				width: 27.2%;
			}
		}
	}
}

