.tariffs {
	position: relative;
	height: 812px;
	
	&_rectangle {
		position: absolute;
		background: url('./../img/stars-layout.svg') no-repeat center / cover;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: -1;
	}
	
	&_stars {
		background: url('./../img/stars.svg');
	}
	
	&_content {
		padding: 70px 0 55px;
	}
	
	&_title {
		font-weight: bold;
		font-size: 45px;
		line-height: 55px;
		text-align: center;
		color: #FFFFFF;
		margin-bottom: 57px;
	}
	
	&_wrapper {
		display: flex;
		justify-content: left;
		align-items: flex-end;
	}
	
	&_type-list {
		width: 51%;
	}
	
	&_type-item {
		position: relative;
		width: 145px;
		height: 145px;
		background: linear-gradient(0deg, rgba(205, 217, 249, 0.65) -104.79%, rgba(205, 217, 249, 0) 122.6%);
		border-radius: 26px;
		margin-bottom: 70px;
		transition: 300ms ease-out;
		
		&:last-child {
			margin-bottom: 0;
		}
		
		&:hover {
			cursor: pointer;
		}
		
		&.active {
			width: 339px;
			box-shadow: 0px 20px 40px rgba(59, 86, 215, 0.4);
			background: linear-gradient(23.15deg, rgba(205, 217, 249, 0.9) -14.09%, rgba(205, 217, 249, 0) 110.25%);
			
			.tariffs_type-info * {
				color: #3B56D7;
			}
		}
	}
	
	&_starship {
		position: absolute;
		
		&__small {
			top: 24px;
			left: 26px;
		}
		
		&__middle {
			top: -52px;
			left: 5px;
		}
		
		&__large {
			top: -71px;
			left: -8px;
		}
	}
	
	&_type-info {
		position: absolute;
		left: 200px;
		top: 44px;
		color: white;
	}
	
	&_type-title {
		font-size: 20px;
		line-height: 24px;
		margin-bottom: 5px;
	}
	
	&_type-name {
		font-weight: bold;
		font-size: 24px;
		line-height: 29px;
		text-transform: uppercase;
		color: #FFFFFF;
	}
	
	&_info {
		width: 49%;
		padding-bottom: 45px;
	}
	
	&_info-title {
		font-weight: normal;
		font-size: 36px;
		line-height: 44px;
		margin-bottom: 25px;
		
		span {
			margin-left: 5px;
			font-weight: bold;
			font-size: 42px;
			line-height: 51px;
			text-transform: uppercase;
		}
	}
	
	&_info-price-wrapper {
		display: flex;
		margin-bottom: 25px;
	}
	
	&_info-price-item {
		max-width: calc(50% - 15px);
		display: flex;
		align-items: flex-start;
		margin-right: 30px;
		
		&:last-child {
			margin: 0;
		}
	}
	
	&_info-price-img {
		margin-right: 10px;
	}
	
	&_info-price-box {
		margin-right: 30px;
		
		&:last-child {
			margin-right: 0;
		}
		
		.big-price {
			font-weight: bold;
			font-size: 24px;
			line-height: 16px;
			margin-bottom: 7px;
		}
		
		.text {
			font-size: 18px;
			line-height: 22px;
		}
	}
	
	&_info-text {
		max-width: 95%;
		min-height: 174px;
		font-size: 22px;
		line-height: 135%;
		color: #8C8C8C;
		margin-bottom: 40px;
	}
	
	&_button-group {
		opacity: 1 !important;
	}
	
	&_info-button {
		width: 249px;
		display: inline-block;
		padding: 15px 0;
		margin-right: 20px;
		text-align: center;
		font-weight: bold;
		font-size: 24px;
		line-height: 29px;
		
		&:last-child {
			margin-right: 20px;
		}
		 
		
		&__transparent {
			color: #00CA69;
			border: 1px solid #00CA69;
			border-radius: 4px;
		}
	}
}

