.advantages {
	background: #f0f3ff;
	padding: 70px 0 80px;

	&_content {
		display: flex;
		align-items: flex-start;
	}

	&_info-block {
		width: calc(100% - 538px);
		margin-right: 53px;
	}

	&_img-block {
		width: 480px;
		padding-top: 45px;
	}

	&_title {
		font-weight: bold;
		font-size: 45px;
		line-height: 55px;
		margin-bottom: 32px;
	}

	&_list li {
		margin-bottom: 20px;
	}

	&_list-item {
		display: flex;
		align-items: center;
		width: fit-content;

		&:last-child {
			margin-bottom: 0;
		}

		svg {
			margin-right: 19px;
		}

		span {
			font-size: 22px;
			line-height: 26px;
			text-transform: uppercase;
			color: #8c8c8c;
		}

		&.active span {
			color: #3b56d7;
		}

		&:hover {
			cursor: pointer;

			span {
				color: #3b56d7;
			}
		}
	}

	&_img {
		margin-bottom: 16px;
		width: 100%;
		object-fit: cover;
	}

	&_img-text {
		font-size: 24px;
		line-height: 130%;
		color: #8c8c8c;
	}

	&_mobile-text {
		display: none;
	}
}

// ===============================
// =========== @Media ============
// ===============================

@media (max-width: $xl) {
	.advantages {
	}
}

@media (max-width: $lg) {
	.advantages {
	}
}

@media (max-width: $md) {
	.advantages {
		padding: 60px 0 55px;

		&_info-block {
			width: calc(100% - 403px);
			margin-right: 49px;
		}

		&_img-block {
			width: 354px;
			padding-top: 62px;
		}

		&_title {
			width: 200%;
			max-width: 520px;
			font-size: 36px;
			line-height: 44px;
			margin-bottom: 28px;
		}

		&_list li {
			margin-bottom: 15px;
		}

		&_list-item {
			display: flex;
			align-items: flex-start;
			width: fit-content;

			svg {
				width: 40px;
				height: 40px;
				margin-right: 15px;
			}

			span {
				position: relative;
				top: 10px;
				font-size: 16px;
				line-height: 19px;
			}

			&:hover {
				cursor: pointer;

				span {
					color: #3b56d7;
				}
			}
		}

		&_img {
			margin-bottom: 33px;
		}

		&_img-text {
			font-size: 16px;
		}
	}
}

@media (max-width: $sm) {
	.advantages {
		padding: 35px 0 72px;
		position: relative;

		&_info-block {
			width: 100%;
			margin: 0;
		}

		&_img-block {
			display: none;
		}

		&_mobile-text {
			display: block;
			font-size: 16px;
			line-height: 130%;
			color: #8c8c8c;
		}

		&_title {
			width: 100%;
			font-size: 20px;
			line-height: 24px;
			margin-bottom: 15px;
		}

		&_list li {
			margin-bottom: 0px;
		}

		&_list-item {
			display: flex;
			align-items: flex-start;
			width: fit-content;
			margin-bottom: 5px;

			svg {
				width: 40px;
				height: 40px;
				margin-right: 15px;
			}

			span {
				position: relative;
				top: 10px;
				font-size: 16px;
				line-height: 19px;
			}

			&:hover {
				cursor: pointer;

				span {
					color: #3b56d7;
				}
			}
		}

		.slick-dots {
			position: absolute;
			display: flex;
			width: 100%;
			justify-content: center;
			bottom: 30px;
			z-index: 1;
			display: flex;
			justify-content: center;
			background: none;

			li {
				margin: 0 8px 0 0;

				&:last-child {
					margin-right: 0;
				}
			}

			li {
				button {
					width: 10px;
					height: 10px;
					border: 1px solid #ababab;
					border-radius: 50%;
					font-size: 0;
					padding: 0;
					background: transparent;
				}

				&.slick-active button {
					background: #ababab;
				}
			}
		}
	}
}
