.contacts {
	margin-bottom: 40px;

	&_title {
		margin-bottom: 27px;
	}

	&_city-choose {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-bottom: 54px;

		.nice-select {
			width: 244px !important;
			height: 40px !important;
			font-size: 20px !important;
		}

		.list {
			width: 244px !important;
		}
	}

	&_city-choose-text {
		font-weight: 500;
		font-size: 20px;
		line-height: 20px;
		letter-spacing: 0.4px;
		color: #8c8c8c;
		margin-right: 9px;
	}

	&_info-wrapper {
		border-bottom: 1px solid #dedede;
		margin-bottom: 35px;
	}

	&_info {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr 1fr;
		margin-bottom: 55px;
	}

	&_info-item {
		position: relative;
		border-right: 1px solid #dedede;
		padding: 0 43px;

		&:first-child {
			padding-left: 0;

			svg {
				left: 0;
			}
		}

		&:last-child {
			padding-right: 0;
			border: 0;
		}

		svg {
			position: absolute;
			top: 0;
			left: 43px;
		}

		.text-content {
			padding-top: 57px;

			.text {
				font-weight: 500;
				font-size: 24px;
				line-height: 29px;
				margin-bottom: 5px;

				&:last-child {
					margin-bottom: 0;
				}

				& + .text {
					margin-top: 13px;
				}

				&__gray {
					font-size: 20px;
					line-height: 24px;
					color: #b4b4b4;
					margin-bottom: 10px;

					&:last-child {
						margin-bottom: 0;
					}
				}
			}
		}
	}

	&_social {
		display: flex;
		align-items: center;
		justify-content: center;
	}

	&_social-text {
		font-weight: 500;
		font-size: 24px;
		line-height: 24px;
		color: #8c8c8c;
		letter-spacing: 0.6px;
		margin-right: 28px;
	}

	&_social-link {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 40px;
		height: 40px;
		background: #3b56d7;
		border-radius: 6px;
		margin-right: 15px;

		&__ig svg {
			position: relative;
			top: 0;
			left: 1px;
		}

		&:last-child {
			margin-right: 0;
		}
	}
}

.contact-form {
	position: relative;
	background: #363d43;
	padding: 58px 0 67px 0;
	margin-bottom: 54px;

	&_map {
		position: absolute;
		left: 0;
		top: 0;
		right: 50%;
		bottom: 0;

		svg {
			position: absolute;
			top: 50%;
			left: 50%;
		}
	}

	&_form-group {
		margin-left: 50%;
		padding-left: 110px;
	}
}

.legal-data {
	margin-bottom: 63px;

	&_title {
		font-weight: bold;
		font-size: 45px;
		line-height: 50px;
		margin-bottom: 45px;
		text-align: center;
	}

	&_wrapper {
		display: flex;
		align-items: flex-start;
		justify-content: left;
	}

	&_info {
		width: 248px;
		min-width: 248px;
		margin-right: 52px;

		p {
			font-weight: 500;
			font-size: 24px;
			line-height: 29px;
			margin-bottom: 11px;

			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	&_link {
		display: block;
		display: flex;
		align-items: flex-start;
		padding-top: 5px;

		&:hover {
			cursor: pointer;
		}
	}

	&_link-img {
		margin-right: 19px;
	}

	&_link-text {
		position: relative;
		top: -1px;
		font-weight: 500;
		font-size: 24px;
		line-height: 24px;
		color: #3b56d7;
	}
}

@media (max-width: $lg) {
	.contacts {
		&_info-item {
			padding: 0 30px;

			.text-content {
				.text {
					font-size: 20px;
					line-height: 24px;

					&__gray {
						font-size: 18px;
						line-height: 22px;
					}
				}
			}
		}

		&_social-text {
			font-size: 22px;
			line-height: 22px;
		}
	}

	.contact-form {
		&_form-group {
			margin-left: 50%;
			padding-left: 20px;
		}
	}

	.legal-data {
		margin-bottom: 43px;

		&_title {
			font-size: 36px;
			line-height: 42px;
			margin-bottom: 40px;
		}

		&_info {
			width: 248px;
			min-width: 248px;
			margin-right: 42px;

			p {
				font-size: 22px;
				line-height: 26px;
			}
		}

		&_link-text {
			font-size: 22px;
			line-height: 22px;
		}
	}
}

@media (max-width: $md) {
	.contacts {
		&_info-item {
			padding: 0 20px;

			.text-content {
				.text {
					font-size: 16px;
					line-height: 20px;

					&__gray {
						font-size: 15px;
						line-height: 18px;
					}
				}
			}
		}
	}
}

@media (max-width: $sm) {
	.contacts {
		margin-bottom: 34px;

		&_title {
			margin-bottom: 22px;
		}

		&_city-choose {
			display: block;
			margin-bottom: 82px;

			.nice-select {
				width: 100% !important;
				height: 40px !important;
				font-size: 18px !important;
			}

			.list {
				width: 100% !important;
			}
		}

		&_city-choose-text {
			text-align: left;
			font-size: 18px;
			line-height: 18px;
			letter-spacing: 1.2px;
			margin: 0 0 11px 0;
		}

		&_info-wrapper {
			border-bottom: 0;
			margin-bottom: 0;
		}

		&_info {
			display: block;
			margin-bottom: 0;
		}

		&_info-item {
			position: relative;
			border-right: 0;
			padding: 0;
			margin-bottom: 27px;

			&:last-child {
				margin-bottom: 22px;
			}

			svg {
				position: absolute;
				top: 0;
				left: 0;
			}

			.text-content {
				padding-top: 31px;

				.text {
					font-size: 20px;
					line-height: 24px;
					margin-bottom: 0px;

					&:last-child {
						margin-bottom: 0;
					}

					& + .text {
						margin-top: 5px;
					}

					&__gray {
						font-size: 20px;
						line-height: 24px;
						color: #b4b4b4;
						margin-bottom: 11px;
					}
				}
			}
		}

		&_social-text {
			font-size: 18px;
			line-height: 18px;
			margin-right: 8px;
			letter-spacing: 0.6px;
		}

		&_social-link {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 26px;
			height: 26px;
			background: #3b56d7;
			border-radius: 6px;
			margin-right: 10px;

			&__ig svg {
				position: relative;
				top: 1px;
				left: 1px;
			}
		}
	}

	.contact-form {
		position: relative;
		background: #363d43;
		padding: 0px 0 21px 0;
		margin-bottom: 12px;

		&_map {
			position: static;
			width: 100%;
			height: 280px;
		}

		&_form-group {
			margin-left: 0;
			padding-left: 0;
		}
	}

	.legal-data {
		margin-bottom: 60px;

		&_title {
			font-weight: bold;
			font-size: 24px;
			line-height: 50px;
			margin-bottom: 10px;
			text-align: center;
		}

		&_wrapper {
			display: block;
		}

		&_info {
			width: 100%;
			min-width: 100%;
			margin-right: 0;
			margin-bottom: 22px;

			p {
				font-weight: 500;
				font-size: 20px;
				line-height: 24px;
				margin-bottom: 11px;

				&:last-child {
					margin-bottom: 0;
				}
			}
		}

		&_link {
			padding-left: 5px;
		}

		&_link-text {
			font-size: 20px;
			line-height: 24px;
		}
	}
}
