.other-articles {
	margin-bottom: 81px;
	
	&__bottom100 {
		margin-bottom: 100px;
	}

	&_title {
		margin-bottom: 33px;
		text-align: center;
		
		&__left {
			text-align: left;
			margin-bottom: 20px;
		}
	}

	&_wrapper {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr;
		grid-gap: 20px;
		
		&__mobile-tile {
			margin-bottom: 40px;
		}
	}

	&_item {
		background: #ffffff;
		box-shadow: 0px 20px 50px rgba(59, 86, 215, 0.25);
		border-radius: 8px;

		&:hover {
			.other-articles_img {
				opacity: 0.85;
			}

			.other-articles_item-title {
				opacity: 0.85;
			}
		}
	}

	&_img {
		width: 100%;
	}

	&_item-container {
		padding: 15px 11px 20px 18px;
	}

	&_item-title {
		font-size: 24px;
		line-height: 29px;
		color: #3b56d7;
		margin-bottom: 27px;
	}

	&_item-footer {
		font-size: 18px;
		line-height: 22px;
		color: #8c8c8c;

		div {
			position: relative;

			&:first-child {
				margin-bottom: 4px;
			}
		}

		svg {
			position: absolute;
			left: 0;
		}

		span {
			margin-left: 29px;
		}

		.time {
			display: inline-block;
			margin-right: 21px;

			svg {
				top: 2px;
			}
		}

		.view {
			display: inline-block;

			svg {
				top: 5px;
			}
		}
	}
	
	&_more-button {
		display: block;
		width: 328px;
		height: 60px;
		line-height: 60px;
		margin: 0 auto;
		padding: 0;
		text-align: center;
		font-weight: bold;
		font-size: 24px;
	}
}

// ===============================
// =========== @Media ============
// ===============================

@media (max-width: $md) {
	.other-articles {
		margin-bottom: 84px;

		&_title {
			margin-bottom: 33px;
			text-align: center;
		}

		&_wrapper {
			display: block;
			margin: 0 -16px;
		}

		.slick-slide {
			padding: 0 16px;
		}

		.slick-dots {
			display: flex;
			justify-content: center;
			align-items: center;

			li {
				margin-right: 18px;
				opacity: 0.5;

				&.slick-active {
					opacity: 1;
				}

				&:last-child {
					margin: 0;
				}

				button {
					display: block;
					width: 16px;
					height: 16px;
					background: #c4c4c4;
					border-radius: 50%;
					font-size: 0;
				}
			}
		}

		&_item {
			box-shadow: 0px 20px 50px rgba(59, 86, 215, 0.25);
			margin-bottom: 50px;
		}

		&_item-container {
			padding: 19px 13px 24px 18px;
		}

		&_item-title {
			font-size: 22px;
			line-height: 27px;
			margin-bottom: 12px;
		}

		&_item-footer {
			font-size: 16px;
			line-height: 19px;
			color: #8c8c8c;

			.date {
				svg {
					top: -2px;
				}
			}

			.time {
				display: inline-block;
				margin-right: 35px;

				svg {
					top: -1px;
				}
			}

			.view {
				display: inline-block;

				svg {
					top: 3px;
				}
			}
		}
	}
}

@media (max-width: $sm) {
	.other-articles {
		margin-bottom: 20px;
		
		&__bottom100 {
			margin-bottom: 50px;
		}

		&_title {
			margin-bottom: 20px;
			text-align: left;
		}

		&_wrapper {
			display: block;
			margin: 0 -16px;
			
			&__mobile-tile {
				display: grid;
				margin: 0 0 30px 0;
				grid-template-columns: 1fr;
			}
		}

		.slick-slide {
			padding: 0 16px;
		}

		.slick-dots {
			position: relative;
			bottom: 55px;

			li {
				margin-right: 12px;
				width: 8px;
				height: 8px;

				button {
					display: block;
					width: 8px;
					max-width: 8px;
					height: 8px;
					font-weight: 0;
					padding: 0;
				}
			}
		}

		&_item {
			box-shadow: 0px 20px 50px rgba(59, 86, 215, 0.25);
			margin-bottom: 68px;
			
			&__mobile-tile {
				margin: 0;
			}
		}

		&_img {
			width: 100%;
			height: 185px;
			object-fit: cover;
			border-radius: 8px 8px 0px 0px;
		}

		&_item-container {
			padding: 15px 13px 21px 16px;
		}

		&_item-title {
			font-size: 18px;
			line-height: 22px;
			margin-bottom: 14px;
		}

		&_item-footer {
			font-size: 18px;
			line-height: 22px;
			color: #8c8c8c;

			.date {
				display: inline-block;
				margin-right: 15px;

				svg {
					top: -2px;
				}
			}

			.time {
				display: block;
				margin-right: 0;

				svg {
					top: -1px;
				}
			}

			.view {
				display: inline-block;

				svg {
					top: 3px;
				}
			}
		}
		
		&_more-button {
			width: 100%;
			height: 40px;
			line-height: 40px;
			font-size: 20px;
		}
	}
}
