.request-form {
	&_title {
		font-weight: bold;
		font-size: 45px;
		line-height: 50px;
		text-align: center;
		color: #ffffff;
		margin-bottom: 35px;
	}

	.request-form_input-group {
		display: grid;
		grid-template-columns: 1fr 1fr;
		grid-column-gap: 12px;
	}

	input {
		display: block;
		height: 40px;
		width: 100%;
		margin-bottom: 14px;
		border-radius: 4px;
		border: 0;
		padding: 13px 17px 11px;
		outline: none;
		font-size: 20px;
		line-height: 24px;
	}

	textarea {
		display: block;
		height: 120px;
		width: 100%;
		margin-bottom: 30px;
		border-radius: 4px;
		border: 0;
		padding: 10px 17px 10px;
		outline: none;
		resize: none;
		font-size: 20px;
		line-height: 24px;
	}

	&_button {
		display: block;
		text-align: center;
		font-weight: bold;
		font-size: 24px;
		line-height: 29px;
		color: #ffffff;
		padding: 16px 0;
		background: #00ca69;
		border-radius: 4px;
		margin-bottom: 18px;

		&:hover {
			cursor: pointer;
		}
	}

	&_text {
		font-size: 24px;
		line-height: 29px;
		color: #ffffff;
		text-align: center;
	}
}

@media (max-width: $sm) {
	.request-form {
		padding-top: 12px;

		&_title {
			font-size: 28px;
			line-height: 50px;
			margin-bottom: 15px;
		}

		.request-form_input-group {
			display: block;
		}

		input {
			display: block;
			height: 40px;
			width: 100%;
			margin-bottom: 16px;
			border-radius: 4px;
			border: 0;
			padding: 13px 17px 11px;
			outline: none;
			font-size: 20px;
			line-height: 24px;
		}

		textarea {
			display: block;
			height: 180px;
			width: 100%;
			margin-bottom: 16px;
			border-radius: 4px;
			border: 0;
			padding: 10px 17px 10px;
			outline: none;
			resize: none;
			font-size: 20px;
			line-height: 24px;
		}

		&_button {
			font-size: 20px;
			line-height: 24px;
			padding: 9px 0;
			border-radius: 4px;
			margin-bottom: 10px;
		}

		&_text {
			max-width: 95%;
			margin: 0 auto;
			font-size: 18px;
			line-height: 22px;
			letter-spacing: 0.3px;
		}
	}
}
