.cost-calc {
	padding-top: 20px;
	margin-bottom: 100px;
	
	&_title {
		font-size: 40px;
		line-height: 49px;
		margin-bottom: 12px;
		text-align: center;
	}
	
	&_text {
		max-width: 810px;
		margin: 0 auto;
		font-size: 20px;
		line-height: 24px;
		margin-bottom: 39px;
		text-align: center;
	}
	
	&_checkbox-title {
		font-weight: 600;
		font-size: 20px;
		line-height: 24px;
		text-align: center;
		margin-bottom: 17px;
	}
	
	&_inputs-wrapper {
		width: 780px;
		margin: 0 auto 30px auto;
		display: grid;
		grid-template-columns: 1fr 1fr;
		grid-column-gap: 20px;
		grid-row-gap: 15px;
	}
	
	&_checkbox-wrapper {
		padding-top: 15px;
	}

	&_checkboxes {
		display: flex;
		justify-content: left;
		padding-left: 81px;
		
		.checkbox {
			margin-right: 30px;
			
			&:last-child {
				margin: 0;
			}
		}
	}
	
	&_form {
		
		input {
			display: block;
			width: 100%;
			height: 54px;
			font-weight: 600;
			font-size: 20px;
			line-height: 54px;
			background: #F7F7F7;
			border: none;
			outline: none;
			padding: 0 20px;
		}
	}
	
	&_button {
		display: block;
		width: 400px;
		height: 80px;
		font-weight: bold;
		line-height: 80px;
		font-size: 24px;
		margin: 0 auto 20px auto;
	}
	
	&_confidential {
		display: block;
		width: 590px;
		margin: 0 auto;
		text-align: center;
		opacity: 0.7;
		font-size: 17px;
		line-height: 23px;
		color: #333333;
	}
}

// ===============================
// =========== @Media ============
// ===============================

@media (max-width: $md) {
	
	.cost-calc {
		
		&_inputs-wrapper {
			width: 100%;
		}
	}
}

@media (max-width: $sm) {
	
	.cost-calc {
		padding-top: 0px;
		margin-bottom: 50px;
		
		&_title {
			text-align: left;
			font-size: 28px;
			line-height: 34px;
			margin-bottom: 8px;
		}
		
		&_text {
			font-size: 20px;
			line-height: 24px;
			margin-bottom: 20px;
			text-align: left;
		}
		
		&_checkbox-title {
			max-width: 70%;
			margin: 0 auto 13px auto;
			text-align: center;
			font-weight: normal;
			font-size: 18px;
			line-height: 22px;
		}
		
		&_inputs-wrapper {
			width: 100%;
			grid-template-columns: 1fr;
			grid-gap: 10px;
		}
		
		&_checkbox-wrapper {
			padding-top: 10px;
		}
	
		&_checkboxes {
			padding: 0;
			justify-content: center;
			
			.checkbox {
				
			}
		}
		
		&_form {
			
			input {
				font-size: 16px;
				height: 60px;
				line-height: 60px;
			}
		}
		
		&_button {
			width: 100%;
			height: 60px;
			font-size: 20px;
			line-height: 60px;
			margin-bottom: 10px;
		}
		
		&_confidential {
			width: 90%;
			margin: 0 auto;
			font-size: 14px;
			line-height: 21px;
		}
	}
}
