// Прогресс бар
.cont {
	height: 100%;
	width: 100%;
	padding: 0;
	margin: 0 auto;
	position: relative;

	#progress-bar {
		position: relative;
		width: 100%;
		background: #cccccc;
		height: 2px;
		margin: 0 auto;
		z-index: -10;
	}

	span {
		box-sizing: border-box;
		height: 65px;
		width: 65px;
		border-radius: 50%;
		background: white;
		border: 1px solid #cccccc;
		position: absolute;
		left: 0;
		top: -20px;
		cursor: pointer;
		transition: all 0.3s ease-in-out;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 28px;
		line-height: 34px;
		color: #cccccc;

		svg {
			fill: #cccccc;
		}

		&:after {
			content: "";
			display: block;
			position: absolute;
			width: 0px;
			min-width: 0px;
			height: 0px;
			border-radius: 50%;
			background: rgb(204, 244, 225);
			z-index: 1;
			border: none;
			transition: 400ms ease-out;
		}

		&.state-active {
			background: #00ca69;
			border: 0;
			color: white;

			svg {
				fill: white;
			}

			&::after {
				content: "";
				display: block;
				position: absolute;
				width: 95px;
				min-width: 95px;
				height: 95px;
				border-radius: 50%;
				background: rgb(204, 244, 225);
				z-index: -1;
				border: none;
			}
		}

		&:last-child.state-active::before {
			content: "";
			position: absolute;
			background: repeating-linear-gradient(to left, #fff, #fff 5px, transparent 5px, transparent 10px);
			width: 150px;
			height: 5px;
			left: -150px;
			z-index: -5;
		}

		&.blue {
			background: #3b56d7;
			color: white;
		}
	}

	.firstOf6 {
		left: 0;
	}

	.secondOf6 {
		left: calc(22% - 32px);
	}

	.thirdOf6 {
		left: calc(41% - 32px);
	}

	.fourthOf6 {
		left: calc(59% - 32px);
	}

	.fifthOf6 {
		left: calc(78% - 32px);
	}

	.sixthOf6 {
		left: calc(100% - 65px);
	}

	#progress-bar::-webkit-progress-value {
		background: #3b56d7;
		transition: all 0.4s ease-in-out;
	}

	#progress-bar::-webkit-progress-bar {
		background: #cccccc;
	}
}

