			line-height: 27px;
			margin-bottom: 15px;
		}
	
		&_info-text {
			max-width: 680px;
			font-size: 18px;
		}
	}
	
	.cont {
		height: 100%;
		width: 98%;
		padding: 0;
		margin: 0;
		position: relative;
	
		#progress-bar {
			position: relative;
			width: 100%;
			background: #cccccc;
			height: 2px;
			margin: 0 auto;
			z-index: -10;
		}
	
		span {
			height: 55px;
			width: 55px;
			top: -15px;
	
			&.state-active {
	
				&::after {
					content: "";
					width: 90px;
					min-width: 90px;
					height: 90px;
				}
			}
	
			&:last-child.state-active::before {
				content: "";
				width: 100px;
				left: -100px;
			}
	
			&.blue {
				background: #3b56d7;
				color: white;
			}
		}
	
		.firstOf6 {
			left: 0;
		}
	
		.secondOf6 {
			left: calc(22% - 27px);
		}
	
		.thirdOf6 {
			left: calc(41% - 27px);
		}
	
		.fourthOf6 {
			left: calc(59% - 27px);
		}
	
		.fifthOf6 {
			left: calc(78% - 27px);
		}
	
		.sixthOf6 {
			left: calc(100% - 55px);
		}
	}
}

