	.cont {
		height: 100%;
		width: 200%;
		padding: 0;
		margin: 0;
		position: relative;
		left: 0;
		transition: 300ms;
	
		#progress-bar {
			position: relative;
			width: 100%;
			background: #cccccc;
			height: 2px;
			margin: 0 auto;
			z-index: -10;
		}
	
		span {
			height: 40px;
			width: 40px;
			top: -10px;
			font-size: 18px;
			line-height: 22px;
	
			&.state-active {
	
				&::after {
					content: "";
					width: 60px;
					min-width: 60px;
					height: 60px;
