				color: white;
			}
		}
	
		.firstOf6 {
			left: 0;
		}
	
		.secondOf6 {
			left: calc(22% - 20px);
		}
	
		.thirdOf6 {
			left: calc(41% - 20px);
		}
	
		.fourthOf6 {
			left: calc(59% - 20px);
		}
	
		.fifthOf6 {
			left: calc(78% - 20px);
		}
	
		.sixthOf6 {
			left: calc(100% - 40px);
		}
	}
}
