.green-dot-list {
	li {
		position: relative;
		margin-left: 42px;
		font-size: 20px;
		line-height: 24px;
		margin-bottom: 5px;

		@media (max-width: 540px) {
			font-size: 20px;
			line-height: 24px;
			margin-left: 37px;
			margin-bottom: 10px;
		}

		&:last-child {
			margin-bottom: 0;
		}

		&::before {
			content: "";
			width: 10px;
			height: 10px;
			border-radius: 50%;
			border: 1px solid #00ca69;
			position: absolute;
			left: -20px;
			top: 6px;
		}
	}
}
