.sales {
	padding-bottom: 100px;

	&_title {
		margin-bottom: 20px;
	}

	&_items {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr;
		grid-gap: 20px;
	}

	&_item {
		background: #ffffff;
		box-shadow: 0px 20px 50px rgba(59, 86, 215, 0.25);
		border-radius: 8px;

		&:hover {
			cursor: pointer;
			opacity: 0.8;
		}
	}

	&_img {
		width: 100%;
		max-height: 200px;
		object-fit: cover;
	}

	&_info-wrapper {
		padding: 13px 14px 16px 16px;
	}

	&_item-title {
		font-weight: normal;
		font-size: 24px;
		line-height: 29px;
		color: #3b56d7;
		margin-bottom: 7px;
	}

	&_item-text {
		font-size: 20px;
		line-height: 24px;
		color: #8c8c8c;

		span {
			white-space: nowrap;
		}
	}
}

// ===============================
// =========== @Media ============
// ===============================

@media (max-width: $xl) {
	.sales {
	}
}

@media (max-width: $lg) {
	.sales {
	}
}

@media (max-width: $md) {
	.sales {
		&_item-title {
			font-size: 21px;
			line-height: 25px;
		}

		&_item-text {
			font-size: 18px;
			line-height: 22px;
		}
	}
}

@media (max-width: $sm) {
	.sales {
		padding-bottom: 50px;

		&_title {
			margin-bottom: 24px;
		}

		&_items {
			display: block;
		}

		&_item {
			display: block;
			margin-bottom: 20px;

			&:last-child {
				margin-bottom: 0;
			}
		}

		&_img {
			width: 100%;
			height: 175px;
		}

		&_info-wrapper {
			padding: 10px 16px 18px 16px;
		}

		&_item-title {
			font-size: 20px;
			line-height: 24px;
			margin-bottom: 10px;
		}

		&_item-text {
			font-size: 18px;
			line-height: 22px;
		}
	}
}
