.footer {
	padding-top: 82px;
	padding-bottom: 32px;
	background: #3e53bb;

	&_pad-top-content {
		display: none;

		@media (max-width: 980px) and (min-width: 650px) {
			display: flex;
			align-items: center;
			margin-bottom: 40px;
		}
	}

	&_pad-logo {
		svg {
			width: 194px;
			height: 23px;
			margin-right: 46px;
		}
	}

	&_pad-nav {
		a {
			display: inline-block;
			font-size: 14px;
			line-height: 17px;
			color: #ffffff;
			margin-right: 13px;

			&:last-child {
				margin-right: 0;
			}
		}
	}

	@media (max-width: 980px) and (min-width: 650px) {
		padding-top: 50px;
		padding-bottom: 30px;
	}

	@media (max-width: 650px) {
		padding-top: 23px;
		padding-bottom: 18px;
	}

	&_content {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		margin-bottom: 85px;

		@media (max-width: 980px) and (min-width: 650px) {
			margin-bottom: 57px;
		}

		@media (max-width: 650px) {
			display: block;
			margin-bottom: 23px;
		}
	}

	&_main-block {
		width: 31%;

		@media (max-width: 980px) and (min-width: 650px) {
			width: 229px;
			margin-right: 9px;
		}

		@media (max-width: 650px) {
			width: 100%;
			padding-bottom: 22px;
			border-bottom: 1px solid #ffffff;
		}
	}

	&_logo {
		display: block;
		margin-bottom: 25px;

		@media (max-width: 980px) and (min-width: 650px) {
			display: none;
		}

		@media (max-width: 650px) {
			margin-bottom: 15px;
		}

		svg {
			@media (max-width: 650px) {
				max-width: 224px;
			}
		}

		&:hover {
			cursor: pointer;
		}
	}

	&_adress {
		margin-bottom: 15px;

		@media (max-width: 980px) and (min-width: 650px) {
			font-size: 15px !important;
			line-height: 18px !important;
			margin-bottom: 16px;
		}

		@media (max-width: 650px) {
			margin-bottom: 18px;
		}
	}

	&_contacts-group {
		margin-bottom: 20px;

		@media (max-width: 980px) and (min-width: 650px) {
			margin-bottom: 15px;
		}
	}

	.footer_contacts {
		margin-bottom: 7px;

		@media (max-width: 980px) and (min-width: 650px) {
			font-size: 15px;
			line-height: 18px;
			position: relative;
			bottom: 1px;
			margin-bottom: 9px;

			&:first-child {
				margin-bottom: 3px;
			}
		}

		svg {
			position: relative;
			top: 2px;
			margin-right: 8px;

			@media (max-width: 980px) and (min-width: 650px) {
				transform: scale(0.85);
				top: 3px;
			}
		}
	}

	&_socials {
		display: flex;
		align-items: center;

		a {
			margin-right: 12px;
		}
	}

	&_column {
		display: block;

		@media (max-width: 650px) {
			padding: 15px 0 16px 0;
			border-bottom: 1px solid #ffffff;

			&:last-child {
				border: none;
				padding-bottom: 0;
			}
		}

		.footer_text {
			margin-bottom: 22px;

			@media (max-width: 650px) {
				margin-bottom: 8px;
				font-size: 15px;
				line-height: 18px;
			}

			&.bigger-font {
				@media (max-width: 650px) {
					font-size: 18px;
					line-height: 22px;
				}
			}

			a {
				color: white;
			}

			&:last-child {
				margin-bottom: 0;
			}
		}

		.mobile-grid {
			@media (max-width: 650px) {
				width: 70%;
				display: grid;
				grid-template-columns: 1fr 1fr;
				grid-gap: 8px;
			}

			.footer_text {
				@media (max-width: 650px) {
					// width: 40%;
					margin-bottom: 0;
				}
			}
		}
	}

	&_second-column {
		width: 16%;

		@media (max-width: 980px) and (min-width: 650px) {
			display: none;
		}

		@media (max-width: 650px) {
			width: 100%;
		}
	}

	&_third-column {
		width: 26.7%;

		@media (max-width: 980px) and (min-width: 650px) {
			width: calc(47% - 119px);
		}

		@media (max-width: 650px) {
			width: 100%;
		}
	}

	&_fourth-column {
		width: 26.3%;

		@media (max-width: 980px) and (min-width: 650px) {
			width: calc(53% - 119px);
		}

		@media (max-width: 650px) {
			width: 100%;
		}
	}

	&_text {
		display: block;
		font-size: 18px;
		line-height: 22px;
		color: #ffffff;

		@media (max-width: 980px) and (min-width: 650px) {
			font-size: 14px;
			line-height: 17px;
		}

		@media (max-width: 650px) {
			font-size: 16px;
			line-height: 19px;
		}

		a:hover {
			cursor: pointer;
		}
	}

	&_copyright {
		font-size: 16px;
		line-height: 19px;
		color: $white;
		text-align: center;

		@media (max-width: 980px) and (min-width: 650px) {
			font-size: 15px;
			line-height: 18px;
		}

		@media (max-width: 650px) {
			font-size: 16px;
			line-height: 19px;
		}
	}
}
