.increase-sale {
	background: #3B56D7;
	position: relative;
	padding: 96px 0 100px;
	
	&_img {
		position: absolute;
		width: 470px;
		top: -96px;
		left: 0;
		bottom: 0px;
	}
	
	&_content {
		position: relative;
		z-index: 1;
		padding-left: 200px;
	}
	
	&_title {
		font-size: 55px;
		line-height: 67px;
		font-weight: bold;
		margin-bottom: 22px;
		color: white;
	}
	
	&_text {
		max-width: 850px;
		font-size: 28px;
		line-height: 34px;
		margin-bottom: 42px;
		color: white;
	}
	
	&_button-group {
		display: flex;
	}
	
	&_green-button {
		display: block;
		width: 250px;
		height: 55px;
		margin-right: 19px;
		text-align: center;
		font-size: 24px;
		line-height: 55px;
	}
	
	&_transparent-button {
		display: block;
		width: 498px;
		height: 55px;
		font-weight: bold;
		text-align: center;
		font-size: 24px;
		line-height: 55px;
		color: white;
		border: 1px solid #FFFFFF;
		border-radius: 4px;
		background: transparent;
	}
}

// ===============================
// =========== @Media ============
// ===============================


@media (max-width: $xl) {
	
	.increase-sale {
		
		&_content {
			padding-left: 350px;
		}
	}
}

@media (max-width: 1450px) {
	
	.increase-sale {
		
		&_img {
			position: absolute;
			width: 370px;
			top: auto;
			left: 0;
			bottom: -13px;
			object-fit: cover;
		}
		
		&_content {
			
		}
	}
}


@media (max-width: $lg) {
	
	.increase-sale {
		
		&_content {
			padding-left: 400px;
		}
		
		&_title {
			font-size: 45px;
			line-height: 57px;
		}
		
		&_text {
			font-size: 25px;
			line-height: 30px;
		}
		
		&_button-group {
			display: block;
		}
		
		&_green-button {
			margin-bottom: 15px;
		}
	}
}

@media (max-width: $md) {
	
	.increase-sale {
		padding: 60px 0 40px;
		
		&_content {
			padding-left: 310px;
		}
		
		&_img {
			width: 298px;
			top: auto;
			left: -15px;
			bottom: -13px;
			object-fit: cover;
		}
		
		&_title {
			font-size: 36px;
			line-height: 44px;
			margin-bottom: 15px;
		}
		
		&_text {
			font-size: 20px;
			line-height: 24px;
			margin-bottom: 30px;
		}
		
		&_button-group {
			display: block;
		}
		
		&_green-button {
			font-weight: bold;
			display: block;
			width: 196px;
			height: 50px;
			font-size: 18px;
			line-height: 50px;
		}
		
		&_transparent-button  {
			width: 374px;
			margin-bottom: 15px;
			height: 50px;
			font-size: 18px;
			line-height: 50px;
		}
	}
}

@media (max-width: $sm) {
	
	.increase-sale {
		padding: 36px 0 35px;
		
		&_content {
			padding-left: 0;
		}
		
		&_img {
			display: none;
		}
		
		&_title {
			padding-left: 10px;
			font-size: 20px;
			line-height: 24px;
			margin-bottom: 15px;
		}
		
		&_text {
			padding-left: 10px;
			font-size: 18px;
			line-height: 22px;
			margin-bottom: 24px;
		}
		
		&_button-group {
			display: block;
		}
		
		&_green-button {
			font-weight: bold;
			display: block;
			height: 40px;
			width: 100%;
			font-size: 16px;
			line-height: 40px;
			margin-bottom: 14px;
		}
		
		&_transparent-button  {
			width: 100%;
			height: auto;
			margin-bottom: 0;
			font-size: 16px;
			line-height: 19px;
			padding: 12px 0;
		}
	}
}