.team {
	position: relative;
	padding: 70px 0 0;

	&:after {
		content: "";
		z-index: -1;
		position: absolute;
		top: 91px;
		right: 0;
		bottom: 0;
		left: 0;
		background: url("./../img/team-layer.svg");
	}

	&_content {
		position: relative;
		z-index: 1;
	}

	&_title {
		font-weight: bold;
		text-align: center;
		font-size: 45px;
		line-height: 55px;
		margin-bottom: 45px;
	}

	.slick-slide {
		margin: 0 10px;
	}

	.slick-list {
		margin: 0 -10px;
	}

	&_slider {
	}

	&_slide {
		background: #ffffff;
		box-shadow: 0px 10px 20px rgba(59, 86, 215, 0.25);
		border-radius: 8px;
		margin-bottom: 105px;
		padding: 42px 36px 26px;
	}

	&_slide-item {
		display: block;
		margin: 0 auto 31px auto;
	}

	&_slide-title {
		font-size: 20px;
		line-height: 24px;
		text-align: center;
		margin-bottom: 9px;
	}

	&_slide-text {
		font-size: 20px;
		line-height: 24px;
		text-align: center;
		color: #8c8c8c;
	}

	.slick-arrow {
		position: absolute;
		background: url("./../img/slider-arow-dark.svg") no-repeat center / cover;
		font-size: 0;
		width: 26px;
		height: 46px;
		top: 130px;

		&.slick-disabled {
			opacity: 0.6;

			&:hover {
				cursor: default;
			}
		}

		&.slick-prev {
			left: -170px;
		}

		&.slick-next {
			right: -170px;
			transform: rotate(180deg);
		}

		&:hover {
			cursor: pointer;
		}
	}

	.slick-dots {
		position: absolute;
		bottom: 70px;
		left: calc(50% - 14px);
		z-index: 1;
		display: flex;
		justify-content: center;
		background: none;

		li {
			margin: 0 8px 0 0;

			&:last-child {
				margin-right: 0;
			}
		}

		li {
			button {
				width: 10px;
				height: 10px;
				border: 1px solid #ababab;
				border-radius: 50%;
				font-size: 0;
				padding: 0;
				background: transparent;
			}

			&.slick-active button {
				background: #ababab;
			}
		}
	}
}

// ===============================
// =========== @Media ============
// ===============================

@media (max-width: $xl) {
	.team {
		.slick-arrow {
			&.slick-prev {
				left: -80px;
			}

			&.slick-next {
				right: -80px;
				transform: rotate(180deg);
			}
		}
	}
}

@media (max-width: 1350px) {
	.team {
		.slick-arrow {
			display: none !important;

			&.slick-prev {
				left: -80px;
			}

			&.slick-next {
				right: -80px;
				transform: rotate(180deg);
			}
		}
	}
}

@media (max-width: $lg) {
	.team {
	}
}

@media (max-width: $md) {
	.team {
		position: relative;
		padding: 60px 0 0;

		&:after {
			content: "";
			z-index: -1;
			position: absolute;
			top: 67px;
			right: -192px;
			bottom: 0;
			left: 0;
			background: url("./../img/team-layer-pad.svg");
		}

		&_content {
			position: relative;
			z-index: 1;
		}

		&_title {
			font-size: 36px;
			line-height: 44px;
			margin-bottom: 32px;
		}

		&_slide {
			background: #ffffff;
			margin-bottom: 108px;
			padding: 29px 5px 18px;
		}

		.slick-slide {
			margin: 0 6.5px;
		}

		.slick-list {
			margin: 0 -6.5px;
		}

		&_slide-item {
			width: 148px;
			height: 95px;
			display: block;
			margin: 0 auto 15px auto;
		}

		&_slide-title {
			font-size: 15px;
			line-height: 18px;
		}

		&_slide-text {
			font-size: 14px;
			line-height: 17px;
		}

		.slick-dots {
			position: absolute;
			bottom: 59px;
		}
	}
}

@media (max-width: $sm) {
	.team {
		position: relative;
		padding: 38px 0 0;

		&:after {
			content: "";
			z-index: -1;
			position: absolute;
			top: 67px;
			right: -192px;
			bottom: 0;
			left: 0;
			background: url("./../img/team-layer-pad.svg");
		}

		&_content {
			position: relative;
			z-index: 1;
		}

		&_title {
			font-size: 20px;
			line-height: 20px;
			margin-bottom: 26px;
		}

		&_slide {
			background: #ffffff;
			margin-bottom: 60px;
			padding: 29px 25px 33px;
		}

		.slick-slide {
			margin: 0 10px;
		}

		.slick-list {
			margin: 0 -10px;
		}

		&_slide-item {
			width: 148px;
			height: 95px;
			display: block;
			margin: 0 auto 15px auto;
		}

		&_slide-title {
			margin-bottom: 9px;
		}

		.slick-dots {
			position: absolute;
			width: 100%;
			display: flex;
			justify-content: center;
			bottom: 35px;
			left: auto;
		}
	}
}
