.header {
	&_top-content {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 20px 30px;
		border-bottom: 1px solid #dedede;

		@media (max-width: 980px) {
			padding: 20px 10px 20px 20px;
		}

		@media (max-width: 540px) {
			display: none;
		}
	}

	&_city-block {
		position: relative;
		top: 2px;

		svg {
			position: relative;
			bottom: 1px;
			display: inline-block;
			margin-right: 7px;

			@media (max-width: 980px) {
				width: 10px;
				height: 14px;
				margin-right: 6px;
			}
		}
	}

	&_city {
		display: inline-block;
		font-size: 20px;
		line-height: 24px;
		color: #3b56d7;
		border-bottom: 1px dashed #c4c4c4;
		margin-right: 7px;

		@media (max-width: 980px) {
			position: relative;
			bottom: 2px;
			font-size: 16px;
			line-height: 19px;
			margin-right: 9px;
		}

		&:hover {
			cursor: pointer;
		}
	}

	&_city-all {
		display: inline-block;
		font-size: 16px;
		line-height: 19px;
		color: #b4b4b4;

		@media (max-width: 980px) {
			position: relative;
			bottom: 2px;
			font-size: 13px;
			line-height: 16px;
		}
	}

	&_phone-block {
		padding-right: 10px;
	}

	&_phone-item {
		position: relative;
		display: inline-block;
		padding-right: 57px;
		padding-left: 55px;
		font-size: 20px;
		line-height: 24px;
		color: #3b56d7;

		@media (max-width: 980px) {
			font-size: 16px;
			line-height: 19px;
			padding-left: 15px;
			padding-right: 27px;
		}

		svg {
			position: relative;
			top: 6px;
			margin-right: 5px;

			@media (max-width: 980px) {
				top: 2px;
				width: 14px;
				height: 14px;
			}
		}

		a {
			&:hover {
				cursor: pointer;
			}
		}

		&:after {
			content: "";
			display: block;
			position: absolute;
			background: #dedede;
			width: 1px;
			right: 0px;
			top: -20px;
			bottom: -20px;
		}

		&:first-child {
			padding-left: 0;

			@media (max-width: 1300px) {
				display: none;
			}
		}

		&:last-child {
			padding: 0 33px 0 42px;

			@media (max-width: 980px) {
				padding: 0 0 0 13px;
			}

			&::after {
				display: none;
			}
		}
	}

	&_bottom-content {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 22px 20px 18px 35px;
		border-bottom: 1px solid #dedede;

		@media (max-width: 1300px) and (min-width: 980px) {
			padding: 17px 30px 22px 30px;
		}

		@media (max-width: 980px) {
			padding: 23px 20px 17px 20px;
		}

		@media (max-width: 540px) {
			padding: 27px 20px 24px 20px;
		}
	}

	&_nav-wrapper {
		display: flex;
		align-items: center;
	}

	&_logo {
		display: inline-block;
		margin-right: 75px;

		@media (max-width: 1300px) {
			width: 210px;
			margin-right: 20px;
		}

		@media (max-width: 540px) {
			margin-right: 0;
		}

		svg {
			width: 308px;
			height: 37px;

			@media (max-width: 1300px) {
				width: 210px;
			}

			@media (max-width: 980px) {
				width: 189px;
			}

			@media (max-width: 540px) {
				position: relative;
				top: 1px;
				width: 188px;
				height: 22.5px;
			}
		}
	}

	&_nav {
		display: inline-block;

		@media (max-width: 1300px) and (min-width: 980px) {
			position: relative;
			bottom: 1px;
		}

		@media (max-width: 980px) {
			display: none;
		}

		@media (max-width: 540px) {
			display: none;
		}
	}

	&_nav-item {
		position: relative;
		bottom: 2px;
		display: inline-block;
		font-size: 20px;
		line-height: 24px;
		margin-right: 25px;
		transition: 100ms;
		color: $black;

		@media (max-width: 1300px) {
			font-size: 16px;
			line-height: 19px;
			margin-right: 20px;
		}

		&:last-child {
			margin-right: 0;
		}

		&:hover {
			cursor: pointer;
			color: #3b56d7;

			&::before {
				content: "";
				display: block;
				width: 100%;
				position: absolute;
				top: -38px;
				border-top: 6px solid #3b56d7;

				@media (max-width: 1300px) {
					top: -31px;
				}
			}
		}
	}

	&_nav-dropdown {
		&:after {
			content: "";
			position: relative;
			bottom: 3px;
			display: block;
			border: solid #8c8c8c;
			border-width: 0 1px 1px 0;
			display: inline-block;
			margin-left: 10px;
			padding: 3px;
			transform: rotate(45deg);
			-webkit-transform: rotate(45deg);

			@media (max-width: 1300px) and (min-width: 541px) {
				margin-left: 6px;
			}
		}

		&:hover::after {
			bottom: -2px;
			transform: rotate(45deg) scale(-1, -1);
		}
	}

	&_search {
		width: 22px;
		position: relative;
		top: 5px;

		@media (max-width: 1300px) and (min-width: 541px) {
			width: 18px;
		}

		&:hover {
			fill: #3b56d7;

			&::before {
				top: -41px;

				@media (max-width: 1300px) {
					top: -35px;
				}
			}
		}
	}

	&_button {
		position: relative;
		bottom: 2px;
		display: block;
		padding: 16px 25px 15px 25px;
		font-weight: 500;
		letter-spacing: 0.6px;
		font-size: 20px;
		line-height: 24px;
		color: $white;
		background: $light_green;

		@media (max-width: 1300px) and (min-width: 980px) {
			padding: 14px 13px 13px 13px;
			font-size: 18px;
			line-height: 22px;
		}

		@media (max-width: 980px) {
			padding: 14px 14px 13px 14px;
			font-size: 16px;
			line-height: 19px;
			letter-spacing: 0.1px;
			font-weight: bold;
		}

		@media (max-width: 540px) {
			display: none;
		}
	}

	&_burger-button {
		@media (min-width: 980px) {
			display: none;
		}

		div {
			width: 24px;
			height: 3px;
			background-color: #393d3d;
			margin: 4px 0;
			margin-right: 25px;
			transition: 200ms;

			@media (min-width: 540px) {
				position: relative;
				bottom: 2px;
				width: 32px;
				margin: 6px 30px 6px 0;
			}
		}

		&.change div:nth-child(1) {
			-webkit-transform: rotate(-45deg) translate(-4px, 4px);
			transform: rotate(-45deg) translate(-4px, 4px);
			
			@media (max-width: 767px) {
				-webkit-transform: rotate(-45deg) translate(-2px, 2px);
				transform: rotate(-45deg) translate(-2px, 2px);
			}
		}

		&.change div:nth-child(2) {
			opacity: 0;
		}

		&.change div:nth-child(3) {
			-webkit-transform: rotate(45deg) translate(-9px, -9px);
			transform: rotate(45deg) translate(-9px, -9px);
			
			@media (max-width: 767px) {
				-webkit-transform: rotate(45deg) translate(-8px, -8px);
				transform: rotate(45deg) translate(-8px, -8px);
			}
		}
	}

	.dropdown {
		position: relative;
		display: inline-block;
	}

	.dropdown-content {
		display: none;
		position: absolute;
		left: -19px;
		min-width: 300px;
		z-index: 10;
	}

	.spacer {
		position: relative;
		height: 33px;
		width: 100%;
		background: transparent;
		overflow: hidden;
	}

	.links {
		background: #ffffff;
	}

	.dropdown-content a {
		white-space: nowrap;
		color: black;
		padding: 15px 23px 15px 20px;
		text-decoration: none;
		display: block;
		font-weight: 500;
		font-size: 16px;
		line-height: 19px;
		display: flex;
		align-items: center;
		letter-spacing: -0.015em;
		transition: 50ms;
	}

	.dropdown-content a:hover {
		position: relative;
		background: rgb(238, 240, 250);
	}

	.dropdown:hover .dropdown-content {
		display: block;
	}
}

/* The side navigation menu */
.sidenav {
	height: calc(100% - 78px);
	width: 0;
	position: fixed;
	z-index: 999;
	top: 146px;
	left: 0;
	background-color: rgba(25, 19, 2, 0.8);
	overflow-x: hidden;
	transition: 0.15s;
	overflow-y: scroll;
	border-top: 1px solid #dedede;

	@media (max-width: 540px) {
		top: 78px;
	}
}

.sidenav_content {
	background: white;
	height: auto;
	position: relative;
	overflow: auto;
	padding: 20px 21px 16px 21px;

	ul li {
		display: block;
	}
}

.sidenav_content a.sidenav_link {
	display: inline-block;
	font-size: 18px;
	line-height: 22px;
	margin-bottom: 20px;
	color: #3c3c3c;

	&:last-child {
		margin-bottom: 26px;

		@media (max-width: 540px) {
			margin-bottom: 15px;
		}
	}

	&:hover {
		opacity: 0.8;
	}

	@media (max-width: 540px) {
		font-size: 16px;
		line-height: 19px;
	}
}

.sidenav_input-wrapper {
	display: block;
	position: relative;
	width: 100%;
	max-width: 340px;
	height: 42px;

	@media (max-width: 767px) {
		height: 38px;
	}
}

.sidenav_input {
	display: block;
	width: 100%;
	height: 100%;
	max-width: 340px;
	outline: none;
	border: 0;
	background: #eaeaea;
	border-radius: 2px;
	padding: 0 10px;
}

.sidenav_input-img {
	position: absolute;
	top: 14px;
	right: 14px;

	@media (max-width: 767px) {
		top: 12px;
	}
}
