.modal {
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	z-index: 9999;
	display: none;
	opacity: 0;
	transition: 0.3s linear;
	
	&_take-call {
		
		.modal-overlay {
			align-items: flex-start;
		}
		
		.modal_content {
			margin-top: 115px;
		}
		
		.modal_input-group {
			margin-bottom: 30px;
		}
	}

	&.is-active {
		display: block;
	}

	&.is-visible {
		opacity: 1;
	}

	&_overlay {
		background: rgba(0, 0, 0, 0.7);
		width: 100%;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	&_content {
		padding: 55px 125px;
		width: 750px;
		background: #fff;
		position: relative;
	}

	&_wrapper {
		margin-top: 0px;
	}

	&_close {
		position: absolute;
		top: 20px;
		right: 20px;
		cursor: pointer;

		&:hover {
			opacity: 0.7;
		}
	}

	&_title {
		max-width: 90%;
		margin: 0 auto;
		font-weight: bold;
		font-size: 40px;
		line-height: 115%;
		margin-bottom: 30px;
		text-align: center;
		
		&__full-width {
			width: 620px;
			max-width: 650px;
			margin-left: -60px;
			margin-bottom: 17px;
		}
	}
	
	&_wide-text {
		width: 650px;
		max-width: 650px;
		margin-left: -75px;
		font-weight: 600;
		font-size: 20px;
		line-height: 27px;
		text-align: center;
		color: #9A9A9A;
		margin-bottom: 35px;
	}

	&_input-group {
		margin-bottom: 20px;
	}

	&_input {
		width: 100%;
		border: none;
		box-sizing: border-box;
		padding: 24px 36px;
		font-weight: 600;
		font-size: 20px;
		line-height: 30px;
		background: #f7f7f7;
		margin-bottom: 15px;
		
		&.error {
			padding: 21px 33px;
			border: 3px solid #FF6F80;
		}
		
		&:last-child {
			margin-bottom: 0;
		}

		&:hover,
		&:focus,
		&:active {
		}
	}

	&_button {
		width: 100%;
		font-weight: bold;
		font-size: 24px;
		line-height: 30px;
		text-align: center;
		padding: 25px 0;
		margin-bottom: 17px;
	}

	&_error-text {
		font-size: 17px;
		line-height: 23px;
		text-align: center;
		color: #ff223c;
		
		&.active {
			margin: 3px 0 20px 0;
		}
	}

	&_confidential-text {
		max-width: 80%;
		margin: 0 auto;
		font-size: 17px;
		line-height: 23px;
		text-align: center;
		opacity: 0.7;

		a:hover {
			opacity: 0.7;
		}
	}
	
	&_radio-title {
		font-weight: 600;
		font-size: 20px;
		line-height: 27px;
		text-align: center;
		margin-bottom: 16px;
	}
	
	&_choose-type-radio {
		display: flex;
		justify-content: center;
		margin-bottom: 30px;
		
		.radio-group {
			margin-right: 33px;
			
			&:last-child {
				margin-right: 0;
			}
		}
	}
}

// ================================================
// ================== @Media ======================
// ================================================

@media (max-width: $xl) and (min-width: $sm) {
	
	.modal {
		
		&.modal_get-price {
		
			.modal_content {
				padding: 25px 125px;
			}
		
			.modal_title {
				font-size: 32px;
				
				&__full-width {
					width: 100%;
					max-width: 100%;
					margin-left: 0;
					margin-bottom: 17px;
				}
			}
			
			.modal_wide-text {
				font-size: 18px;
				line-height: 25px;
				margin-bottom: 14px;
			}
		
			.modal_input-group {
				margin-bottom: 12px;
			}
		
			.modal_input {
				padding: 15px 22px;
				font-weight: 600;
				font-size: 16px;
				line-height: 22px;
				margin-bottom: 10px;
				
				&.error {
					padding: 12px 19px;
					border: 3px solid #FF6F80;
				}
				
				&:last-child {
					margin-bottom: 0;
				}
			}
		
			.modal_button {
				font-weight: 600;
				font-size: 18px;
				line-height: 30px;
				padding: 15px 0;
				margin-bottom: 10px;
			}
		
			.modal_error-text {
				font-size: 17px;
				line-height: 23px;
				text-align: center;
				color: #ff223c;
				
				&.active {
					margin: 10px 0 20px 0;
				}
			}
		
			.modal_confidential-text {
				max-width: 90%;
				font-size: 14px;
				line-height: 21px;
			}
		}
	}
	
}

@media (max-width: $sm) {
	
	.modal {
		
		&_take-call {
			
			.modal_content {
				margin-top: 0px;
			}
			
			.modal_input-group {
				margin-bottom: 12px;
			}
		}
	
		&_content {
			padding: 39px 20px 20px 20px;
			width: 100%;
			height: 100%;
			background: #fff;
			position: relative;
			margin-top: 0px;
			overflow-y: scroll;
		}
	
		&_wrapper {
			margin-top: 0px;
		}
	
		&_close {
			top: 15px;
			right: 15px;
			width: 18px;
			height: 18px;
			
			svg {
				width: 18px;
				height: 18px;
			}
		}
	
		&_title {
			font-size: 26px;
			line-height: 32px;
			margin-bottom: 20px;
			
			&__full-width {
				width: 100%;
				max-width: 95%;
				margin: 0 auto 12px;
			}
		}
		
		&_wide-text {
			width: 100%;
			max-width: 100%;
			margin-left: 0;
			font-size: 16px;
			line-height: 23px;
			margin-bottom: 14px;
		}
	
		&_input-group {
			margin-bottom: 12px;
		}
	
		&_input {
			padding: 15px 22px;
			font-weight: 600;
			font-size: 16px;
			line-height: 30px;
			margin-bottom: 10px;
			
			&.error {
				padding: 12px 19px;
				border: 3px solid #FF6F80;
			}
			
			&:last-child {
				margin-bottom: 0;
			}
		}
	
		&_button {
			font-weight: 600;
			font-size: 18px;
			line-height: 30px;
			padding: 15px 0;
			margin-bottom: 10px;
		}
	
		&_error-text {
			font-size: 17px;
			line-height: 23px;
			text-align: center;
			color: #ff223c;
			
			&.active {
				margin: 10px 0 20px 0;
			}
		}
	
		&_confidential-text {
			max-width: 90%;
			font-size: 14px;
			line-height: 21px;
		}
		
		&_radio-title {
			font-size: 16px;
			line-height: 27px;
			margin-bottom: 9px;
		}
		
		&_choose-type-radio {
			display: flex;
			justify-content: center;
			margin-bottom: 20px;
			
			.radio-group {
				margin-right: 43px;
			}
		}
	}
}