.Tags {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	flex-wrap: wrap;

	&__bottom-20px {
		margin-bottom: 20px;
	}

	.tag {
		font-size: 18px;
		line-height: 22px;
		color: #3b56d7;
		background: #e5f1ff;
		border-radius: 5px;
		padding: 5px 14px;
		transition: 200ms;
		margin-right: 20px;
		margin-bottom: 20px;

		&:hover {
			cursor: pointer;
			color: #ffffff;
			background: #3b56d7;
		}
	}
}

// ===============================
// =========== @Media ============
// ===============================

@media (max-width: $sm) {
	.Tags {
		margin-bottom: 16px;

		.tag {
			padding: 7px 9px 4px 9px;
			margin-right: 10px;
			margin-bottom: 10px;
			font-size: 16px;
			line-height: 19px;
		}
	}
}
