.reviews {
	padding-bottom: 100px;
	
	&_title {
		margin-bottom: 40px;
	}

	&_wrapper {
		display: flex;
		margin: 0 -10px 30px -10px;
	}
	
	&_column {
		width: calc((100% - 20px)/3);
		margin: 0 10px;
	}

	&_item {
		background: #ffffff;
		box-shadow: 0px 20px 50px rgba(59, 86, 215, 0.25);
		border-radius: 8px;
		padding: 21px 26px 16px;
		page-break-inside: avoid;
		break-inside: avoid;
		margin-bottom: 20px;
		
		&:last-child {
			margin-bottom: 0;
		}
	}

	&_item-date {
		font-size: 20px;
		line-height: 24px;
		text-align: right;
		color: #8c8c8c;
		margin-bottom: 13px;
	}

	&_item-text {
		font-size: 20px;
		line-height: 24px;
		color: #8c8c8c;
		padding-bottom: 24px;
		border-bottom: 1px solid #dedede;
		margin-bottom: 12px;
		
		p, a {
			display: inline;
		}
		
		a {
			color: #3B56D7;
			
			&:hover {
				opacity: 0.8;
				cursor: pointer;
			}
		}
	}

	&_item-author {
		font-weight: 500;
		font-size: 20px;
		line-height: 24px;
		color: #3c3c3c;
		text-align: right;
	}
	
	&_form {
		padding: 21px 26px 23px;
		background: #FFFFFF;
		box-shadow: 0px 20px 50px rgba(59, 86, 215, 0.25);
		border-radius: 8px;
		margin-bottom: 20px;
	}
	
	&_form-title {
		font-size: 20px;
		line-height: 24px;
		margin-bottom: 13px;
	}
	
	&_input-group {
		margin-bottom: 16px;
		
		input {
			width: 100%;
			height: 40px;
			line-height: 40px;
			font-size: 20px;
			padding: 0 17px;
			background: #FFFFFF;
			border: 1px solid #DEDEDE;
			border-radius: 4px;
			margin-bottom: 14px;
		}
		
		textarea {
			width: 100%;
			height: 160px;
			font-size: 20px;
			line-height: 24px;
			padding: 10px 17px;
			background: #FFFFFF;
			border: 1px solid #DEDEDE;
			border-radius: 4px;
			resize: none;
			overflow-x: hidden;
		}
	}
	
	&_item-show-more.active {
		display: block;
	}
	
	&_button {
		width: 100%;
		height: 60px;
		font-size: 24px;
		line-height: 60px;
		text-align: center;
	}
	
	&_show-more {
		display: block;
		width: 328px;
		margin: 0 auto;
		height: 60px;
		line-height: 60px;
		font-size: 24px;
		font-weight: bold;
		text-align: center;
		
	}
	
	.checkbox {
		margin-bottom: 17px;
	}
	
	.custom-checkbox {
		width: 0;
		position: absolute;
		z-index: -1;
		opacity: 0;
	}

	.custom-checkbox + label {
		position: relative;
		bottom: 3px;
		display: inline-block;
		align-items: center;
		user-select: none;
		font-size: 16px;
		line-height: 19px;
		color: #8C8C8C;
		padding-left: 25px;
	}

	.custom-checkbox + label::before {
		position: absolute;
		top: -2px;
		left: 0;
		content: "";
		display: inline-block;
		width: 18px;
		height: 18px;
		flex-shrink: 0;
		flex-grow: 0;
		border: 1px solid #DFE2E5;
		border-radius: 4px;
		margin-right: 10px;
	}

	.custom-checkbox:checked + label::before {
		background-color: white;
		background: url("./../img/green-v.svg") no-repeat center / cover;
		background-position-y: -1px;
		background-position-x: 1px;
	}
}


// ===============================
// =========== @Media ============
// ===============================

@media (max-width: $md) {
	
	
}

@media (max-width: $sm) {
	
	.reviews {
		padding-bottom: 50px;
		
		&_title {
			margin-bottom: 20px;
		}
	
		&_wrapper {
			display: block;
			margin: 0 0 25px 0;
		}
		
		&_column {
			width: 100%;
			margin: 0;
		}
	
		&_item {
			background: #ffffff;
			box-shadow: 0px 20px 50px rgba(59, 86, 215, 0.25);
			border-radius: 8px;
			padding: 21px 23px 17px;
			page-break-inside: avoid;
			margin-bottom: 20px;
			
			&:last-child {
				margin-bottom: 20px;
			}
			
			&__mobile-hidden {
				display: none;
			}
		}
	
		&_item-date {
			font-size: 16px;
			line-height: 19px;
			margin-bottom: 10px;
		}
	
		&_item-text {
			padding-bottom: 17px;
			margin-bottom: 12px;
			line-height: 22px;
			
			p, a {
				font-size: 18px;
				line-height: 22px;
			}
		}
	
		&_item-author {
			font-size: 18px;
			line-height: 22px;
		}
		
		&_form {
			padding: 21px 20px 17px;
			background: #FFFFFF;
			box-shadow: 0px 20px 50px rgba(59, 86, 215, 0.25);
			border-radius: 8px;
			margin-bottom: 20px;
		}
		
		&_form-title {
			font-size: 20px;
			line-height: 24px;
		}
		
		&_input-group {
			margin-bottom: 16px;
		}
		
		&_button {
			height: 40px;
			font-size: 20px;
			line-height: 40px;
		}
		
		.checkbox {
			margin-bottom: 13px;
		}
		
		&_show-more {
			width:  100%;
			height: 40px;
			line-height: 40px;
			font-size: 20px;
		}
	}
}
