.results {
	position: relative;
	padding: 68px 0 105px;
	background: #f0f3ff;

	&_title {
		font-weight: bold;
		font-size: 45px;
		line-height: 55px;
		margin-bottom: 38px;
		text-align: center;
	}

	&_slide {
		display: flex !important;
		padding: 40px 40px 40px 100px;
		justify-content: space-between;
		background: white;
	}

	&_slide-info {
		max-width: 241px;
		padding-top: 20px;
	}

	&_slide-title {
		font-weight: bold;
		font-size: 22px;
		line-height: 27px;
		margin-bottom: 3px;
	}

	&_slide-text {
		font-size: 20px;
		line-height: 22px;
		margin-bottom: 22px;
	}

	&_slide-price {
		font-weight: bold;
		font-size: 42px;
		line-height: 51px;
		color: #00ca69;
	}

	&_slide-link {
		font-size: 20px;
		line-height: 24px;
		color: #3b56d7;

		&__mobile {
			display: none;
		}
	}

	&_slide-img {
		width: 665px;
		height: 430px;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		&__empty {
			display: flex;
			justify-content: center;
			align-items: center;
			border: 1px solid #cccccc;

			&::after {
				content: "";
				background: url("./../img/empty-result.svg");
				width: 153px;
				height: 92px;
			}
		}
	}

	.slick-arrow {
		position: absolute;
		background: url("./../img/slider-arow-dark.svg") no-repeat center / cover;
		font-size: 0;
		width: 26px;
		height: 46px;
		top: calc(50% - 23px);

		&.slick-disabled {
			opacity: 0.6;

			&:hover {
				cursor: default;
			}
		}

		&.slick-prev {
			left: -170px;
		}

		&.slick-next {
			right: -170px;
			transform: rotate(180deg);
		}

		&:hover {
			cursor: pointer;
		}
	}

	.slick-dots {
		position: absolute;
		display: flex;
		width: 100%;
		justify-content: center;
		bottom: 70px;
		z-index: 1;
		display: flex;
		justify-content: center;
		background: none;

		li {
			margin: 0 8px 0 0;

			&:last-child {
				margin-right: 0;
			}
		}

		li {
			button {
				width: 10px;
				height: 10px;
				border: 1px solid #ababab;
				border-radius: 50%;
				font-size: 0;
				padding: 0;
				background: transparent;
			}

			&.slick-active button {
				background: #ababab;
			}
		}
	}
}

// ===============================
// =========== @Media ============
// ===============================

@media (max-width: $xl) {
	.results {
		.slick-arrow {
			&.slick-prev {
				left: -80px;
			}

			&.slick-next {
				right: -80px;
				transform: rotate(180deg);
			}
		}
	}
}

@media (max-width: 1350px) {
	.results {
		.slick-arrow {
			top: 50%;

			&.slick-prev {
				left: -50px;
			}

			&.slick-next {
				right: -50px;
				transform: rotate(180deg);
			}
		}
	}
}

@media (max-width: $lg) {
	.results {
		&_content {
			width: calc(100% + 40px);
			margin-left: -20px;
		}

		.slick-arrow {
			width: 21px;
			height: 37px;
			z-index: 10;

			&.slick-prev {
				left: 20px;
			}

			&.slick-next {
				right: 20px;
				transform: rotate(180deg);
			}
		}
	}
}

@media (max-width: $md) {
	.results {
		padding: 60px 0 96px;

		&_title {
			font-size: 36px;
			line-height: 44px;
			margin-bottom: 16px;
		}

		&_slide {
			display: flex !important;
			padding: 40px 63px 43px 80px;
			justify-content: space-between;
			background: white;
		}

		&_slide-info {
			max-width: 234px;
			padding-top: 0;
		}

		&_slide-title {
			font-size: 24px;
			line-height: 29px;
			margin-bottom: 1px;
		}

		&_slide-text {
			font-size: 18px;
			line-height: 22px;
			margin-bottom: 22px;
		}

		&_slide-link {
			font-size: 18px;
			line-height: 22px;
		}

		&_slide-img {
			width: 375px;
			height: 244px;

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}

			&__empty {
				&::after {
					content: "";
					background: url("./../img/empty-result.svg") no-repeat center / cover;
					width: 100px;
					height: 57px;
				}
			}
		}

		.slick-dots {
			bottom: 60px;
		}
	}
}

@media (max-width: $sm) {
	.results {
		padding: 35px 16px 69px;

		&_title {
			font-size: 20px;
			line-height: 20px;
			margin-bottom: 24px;
		}

		&_slide {
			display: block !important;
			padding: 0;
			background: transparent;
		}

		&_slide-info {
			max-width: 100%;
			width: 100%;
		}

		&_slide-title {
			font-size: 20px;
			line-height: 24px;
			margin-bottom: 3px;
		}

		&_slide-text {
			max-width: 85%;
			font-size: 16px;
			line-height: 19px;
			margin-bottom: 21px;

			&__mobile-bigger {
				max-width: 100%;
				font-size: 18px;
				line-height: 22px;
			}
		}

		&_slide-price {
			font-size: 42px;
			line-height: 45px;
		}

		&_slide-link {
			display: none;
			font-size: 16px;
			line-height: 19px;

			&__mobile {
				display: block;
			}
		}

		&_slide-img {
			background: white;
			width: 100%;
			height: 185px;
			margin-bottom: 19px;

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}

			&__empty {
				height: 185px;

				&::after {
					content: "";
					background: url("./../img/empty-result.svg") no-repeat center / cover;
					width: 100px;
					height: 57px;
				}
			}
		}

		.slick-slide {
			margin: 0 10px;
		}

		.slick-list {
			margin: 0 -10px;
		}

		.slick-dots {
			width: calc(100% - 32px);
			bottom: 35px;
		}
	}
}
